import { Component } from '@angular/core';
// import { NavController, ViewController, NavParams } from 'ionic-angular/index';
import { ViewController, NavParams } from 'ionic-angular';

/**
 * Generated class for the StaffPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
    template: `
    <ion-list radio-group [(ngModel)]="level">
    <ion-item>
    <ion-label class="title-item">cancel</ion-label>
  </ion-item>
    <ion-item>
      <ion-label>すべてのイベント</ion-label>
      <ion-radio (ionSelect)="close()" value="id1"></ion-radio>
    </ion-item>
    <ion-item>
    <ion-label>すべてのイベント</ion-label>
      <ion-radio (ionSelect)="close()" value="id2"></ion-radio>
    </ion-item>
    <ion-item>
    <ion-label> すべてのイベント</ion-label>
      <ion-radio (ionSelect)="close()" value="id3"></ion-radio>
    </ion-item>
  </ion-list>
    `,
    //styleUrls: ['./app/components/facility-popover.scss']
})
export class popUpDetail {
    level : string;
    constructor(public viewCtrl: ViewController,public navParams:NavParams) {
        this.level = this.navParams.get('level');
     }

    close() {
        console.log(this.level);
        this.viewCtrl.dismiss(this.level);
    }
}


