import { Component, ViewChild } from '@angular/core';
import { Navbar, IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http } from '@angular/http';
import { constant } from './../../app/constant';
import { commonClass } from './../../app/common';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { DatePipe } from '@angular/common';

@IonicPage()
@Component({
  selector: 'page-no-ah-playing',
  templateUrl: 'no-ah-playing.html',
})
export class NoAhPlayingPage {
  @ViewChild(Navbar) navBar: Navbar;
  user: any;
  tokenObj: any;
  urlContent: SafeResourceUrl;
  datePipe: any;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private http: Http,
    private common: commonClass,
    private sanitizer: DomSanitizer) {

    this.user = navParams.get('user');
    this.tokenObj = this.common.tokenObj;  
    this.datePipe = new DatePipe('jp');

  }

  /**
  * generate encode url
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  generateEncodedUrl() {

    let paramsUrl = `XX0000=NO0001
                     &userid01=${this.user.Id}&username01=${this.user.UserName}
                     &userid02=-1&username02=
                     &userid03=-1&username03=
                     &userid04=-1&username04=
                     &userid05=-1&username05=
                     &userid06=-1&username06=`;
    return encodeURI(paramsUrl);

  }

  async ionViewDidLoad() {

    await this.common.writeToBehaviorLog(constant.SCREEN_NOAH_CODE, constant.MSG_BEHAVIOR_BLOG);
    var url = await this.common.getContentFile('NO0001', 'main/main.html');
    if (url) {
      var param = this.generateEncodedUrl();
      var param2 = this.datePipe.transform(Date.now(), 'hhmmss');
      url = url + '?' + String(param) + '&currentTime=' + param2;
      this.urlContent = this.sanitizer.bypassSecurityTrustResourceUrl(String(url));
    } else {
      this.urlContent = url;
    }
    // handle event back button to chat mode screen
    this.navBar.backButtonClick = async (e: UIEvent) => {
      this.navCtrl.pop();
    };
  }

  /**
  * commit NoAH history
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async commitNoAhHistories(result) {
    let data = {
      ContentCode: 'NO0001',
      StaffId: this.tokenObj.userObject.Id,
      UserId: result.UserId,
      Score00: result.Score00,
      Score01: result.Score00,
      Score02: result.Score00,
      Score03: result.Score00,
      Score04: result.Score00,
      Score05: result.Score00,
      Score06: result.Score00,
      Score07: result.Score00,
      Score08: result.Score00,
      Score09: result.Score00,
      Score10: result.Score00,
      Score11: result.Score00,
      Score12: result.Score00,
      Score13: result.Score00,
      Scorex01: result.Scorex01,
      Scorex02: result.Scorex02,
      Scorex03: result.Scorex03,
      Scorex04: result.Scorex04,
      Scorex05: result.Scorex05,
      Scorex06: result.Scorex06,
      Scorex07: result.Scorex07,
      Time: result.Time,
      Type: 1
    };
    await this.http.post(constant.API_NOAHHISTORIES, JSON.stringify(data), this.tokenObj.options).toPromise().catch(
      err => {
        console.log(err);
      })
  }
  
}




