import { Component } from '@angular/core';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { NavController, AlertController, LoadingController, Events, PopoverController, Popover } from 'ionic-angular';
import { Http } from '@angular/http';
import { JwtHelper } from 'angular2-jwt';
import { constant } from './../../app/constant';
import { commonClass } from './../../app/common';
import { levelPopoverPage } from './../../components/levelpopover';

/**
 * Generated class for the RegisterUserComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'register-user',
  templateUrl: 'register-user.html'
})

export class RegisterUserComponent {

  public jwtHelper: JwtHelper = new JwtHelper();
  arrayFacility: any;
  arrayStaffItem: any;
  objectFacilities: any;
  UserName: string;
  UserKana: string;
  day: string;
  popover: Popover;
  month: string;
  year: string
  gender: string;
  FacilityId: string;
  StaffId: string;
  level: string;
  public Image: string;
  urlImage: string;
  text: string;
  binaryImg: string;
  tokenObj: { options: any, userObject: any };
  agreementId: string;
  selectOptions = {
    cssClass: '.custom-select'
  };

  constructor(public loadingCtrl: LoadingController,
    public popoverCtrl: PopoverController,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    public camera: Camera,
    private http: Http,
    public common: commonClass,
    private events: Events) {

    // this.commonClass.getToken().then(tokenObj => {
    //   this.tokenObj = tokenObj;
    //   this.agreementId = this.tokenObj.userObject.AgreementId;
    //   this.loadFacilities();
    //   this.loadStaffsList();
    // });
    this.tokenObj = this.common.tokenObj;
    this.agreementId = this.tokenObj.userObject.AgreementId;
    this.loadFacilities();
    this.loadStaffsList();
    this.Image = constant.USER_DEFAULT_IMAGE;

  }

  async ionViewDidLoad() {

    this.common.writeToBehaviorLog(constant.SCREEN_REGISTER_CODE, constant.MSG_BEHAVIOR_BLOG);

  }

  /**
  *  take picture from library
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  takePicfromLibrary() {

    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: 0,
      sourceType: 0,
      targetWidth: 600,
      targetHeight: 400
    };
    this.takePic(options);

  }

  /**
  * take picture from Camera
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  takePicfromCamera() {

    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: 0,
      sourceType: 1,
      targetWidth: 600,
      targetHeight: 400,
      cameraDirection: this.camera.Direction.FRONT
    };
    this.takePic(options);

  }

  /**
  * get image from camera
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async takePic(option) {

    await this.camera.getPicture(option).then((imageData) => {
      let image = constant.SETTING_IMAGE_CAMERA + imageData;
      this.binaryImg = imageData;
      this.Image = image;
    }, (err) => {
    });

  }

  /**
  * load combobox facilities
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async loadFacilities() {

    // this.http.get(constant.API_FACILITIES_GET + "?AgreementId=" + this.agreementId, this.tokenObj.options).map(res => res.json()).subscribe(data => {
    //   this.arrayFacility = data.items;
    // }, (error) => {
    //   let alert = this.alertCtrl.create({
    //     subTitle: constant.NWK_CK_065,
    //     buttons: [constant.BUTTON_OK]
    //   });
    //   alert.present();
    // });
    var data = await this.http.get(constant.API_FACILITIES_GET + "?AgreementId=" + this.agreementId, this.tokenObj.options).toPromise().catch(async error => {
      // if error show meseage       
      await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
    });
    if (data) {
      this.arrayFacility = data.json().items;
    }

  }

  ionViewWillUnload() {

    this.popover && this.popover.dismiss();

  }

  /**
  * load combobox staff
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async loadStaffsList() {

    // this.http.get(constant.API_STAFFS_GET + "?AgreementId=" + this.agreementId, this.tokenObj.options).map(res => res.json()).subscribe(data => {
    //   this.arrayStaffItem = data.items;
    // }, (error) => {
    //   let alert = this.alertCtrl.create({
    //     subTitle: constant.NWK_CK_065,
    //     buttons: [constant.BUTTON_OK]
    //   });
    //   alert.present();
    // });
    var data = await this.http.get(constant.API_STAFFS_GET + "?AgreementId=" + this.agreementId, this.tokenObj.options).toPromise().catch(async error => {
      // if error show meseage       
      await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
    });
    if (data) {
      this.arrayStaffItem = data.json().items;
    }

  }

  /**
  * register user
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  isRegister() {

    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    loading.present();

    let params = {
      Id: 0,
      AgreementId: this.agreementId,
      PhotoURL: '',
      Birthday: this.year + '/' + this.month + '/' + this.day,
      UserName: this.UserName,
      UserKana: this.UserKana,
      Sex: this.gender,
      FacilityId: this.FacilityId,
      StaffId: this.StaffId,
      Level: this.level
    };
    if (!params.Birthday || !params.UserKana
      || !params.UserName || !params.Sex
      || !params.FacilityId || !params.StaffId || !params.Level) {

      let mes = '';

      if (!params.UserKana) mes += constant.USER_KANA_REGISTER_SCREEN;
      if (!params.UserName) mes += constant.USER_NAME_REGISTER_SCREEN;
      if (!params.Sex) mes += constant.SEX_REGISTER_SCREEN;
      if (!this.year || !this.month || !this.day) mes += constant.BIRTHDAY_REGISTER_SCREEN;
      if (!params.FacilityId) mes += constant.FACILITY_ID_REGISTER_SCREEN;
      if (!params.StaffId) mes += constant.STAFF_ID_REGISTER_SCREEN;
      if (!params.Level) mes += constant.LEVEL_REGISTER_SCREEN;
      if (mes.length > 0) mes = mes.substring(0, mes.length - 1);

      mes = constant.NWK_CK_001.replace('{0}', mes);
      let alert = this.alertCtrl.create({
        subTitle: mes,
        buttons: [constant.BUTTON_OK]
      });
      loading.dismiss();
      alert.present();
    }
    else if (params.UserName.length > 10 || this.common.checkIsJapaneseChar(params.UserName) == false) {
      let mes = constant.NAME_REGISTER_SCREEN_TEXT;
      mes = constant.NWK_CK_015.replace('{0}', mes);
      let alert = this.alertCtrl.create({
        subTitle: mes,
        buttons: [constant.BUTTON_OK]
      });
      loading.dismiss();
      alert.present();
    }
    else if (params.UserKana.length > 15 || this.common.checkIsHiraganaChar(params.UserKana) == false) {
      let mes = constant.WONDER_REGISTER_SCREEN_TEXT;
      mes = constant.NWK_CK_016.replace('{0}', mes);
      let alert = this.alertCtrl.create({
        subTitle: mes,
        buttons: [constant.BUTTON_OK]
      });
      loading.dismiss();
      alert.present();
    }
    else {
      this.http.post(constant.API_REGISTER_USER, JSON.stringify(params), this.tokenObj.options)
        .map(res => res.json())
        .subscribe(data => {
          params.Id = data.userId;
          if (this.binaryImg) {
            this.http.post(constant.API_UPLOAD_USER, JSON.stringify({ UserId: params.Id, Binary: this.binaryImg }), this.tokenObj.options)
              .map(res => res.json())
              .subscribe(data => {
                this.urlImage = data.photourl;
                params.PhotoURL = this.urlImage;
                this.http.put(constant.API_UPDATE_USER, JSON.stringify(params), this.tokenObj.options)
                  .subscribe(data => {
                    loading.dismiss();
                    this.goBackAfterRegister();
                    this.events.publish(constant.BACK_TO_USER_LIST);
                  }, error => {
                    let alert = this.alertCtrl.create({
                      subTitle: error.result,
                      buttons: [constant.BUTTON_OK]
                    });
                    loading.dismiss();
                    alert.present();
                  });
              }, error => {
                let alert = this.alertCtrl.create({
                  subTitle: constant.NWK_CK_013,
                  buttons: [constant.BUTTON_OK]
                });
                loading.dismiss();
                alert.present();
              });
          } else {
            loading.dismiss();
            this.goBackAfterRegister();
            this.events.publish(constant.BACK_TO_USER_LIST);
          }
        }, error => {
          let alert = this.alertCtrl.create({
            subTitle: constant.NWK_CK_014,
            buttons: [constant.BUTTON_OK]
          });
          loading.dismiss();
          alert.present();
        });
    }

  }

  /**
  * back to register screen after register user
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  goBackAfterRegister() {

    this.navCtrl.pop();

  }

  /**
  * dismiss register screen
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  goBack() {

    let params = {
      AgreementId: this.agreementId,
      Birthday: this.year + this.month + this.day,
      UserName: this.UserName,
      UserKana: this.UserKana,
      Sex: this.gender,
      FacilityId: this.FacilityId,
      StaffId: this.StaffId,
      Level: this.level
    };
    if (this.binaryImg || params.UserKana
      || params.UserName || params.Sex || this.year || this.month || this.day
      || params.FacilityId || params.StaffId || params.Level) {

      let alert = this.alertCtrl.create({
        subTitle: constant.NWK_CK_017,
        buttons: [
          {
            text: constant.BUTTON_CANCEL_ENG,
            role: constant.BUTTON_CANCEL_ROLE,
            handler: () => {
            }
          },
          {
            text: constant.BUTTON_OK,
            handler: () => {
              this.navCtrl.pop();
            }
          }
        ]
      });
      alert.present();
    }
    else this.navCtrl.pop();

  }

  /**
  * present level pop up
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  levelPopover(event) {

    this.popover = this.popoverCtrl.create(levelPopoverPage, { level: this.level });
    this.popover.present({
      ev: event
    });
    this.popover.onDidDismiss(data => {
      if (data) {
        this.level = data;
      }
    });
    
  }

}



