import { Component, ViewChild } from '@angular/core';
import { Http } from '@angular/http';
import { Navbar, IonicPage, NavController, NavParams } from 'ionic-angular';
import { constant } from '../../app/constant';
import { commonClass } from '../../app/common';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { File } from '@ionic-native/file';
import { DatePipe } from '@angular/common';

@IonicPage()
@Component({
  selector: 'page-playing-content',
  templateUrl: 'playing-content.html',
})
export class PlayingContentPage {
  @ViewChild(Navbar) navBar: Navbar;
  contentId: any;
  contenCode: any;
  contenName: any;
  categoryId: any;
  arrayPeople: any;
  listContents: any;
  tokenObj: any;
  urlContent: SafeResourceUrl;
  currentIndex: any;
  disableButtonNext: Boolean = false;
  datePipe: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: Http,
    private common: commonClass,
    private sanitizer: DomSanitizer,
    public file: File) {
   
    this.tokenObj = this.common.tokenObj;
    this.contentId = navParams.get('contentId');
    this.contenCode = navParams.get('contenCode');
    this.contenName = navParams.get('contenName');
    this.categoryId = navParams.get('categoryId');
    this.listContents = navParams.get('listContents');
    this.arrayPeople = navParams.get('arrayPlayer');
    this.datePipe = new DatePipe('jp');

  }

  async ionViewDidLoad() {

    console.log('ionViewDidLoad PlayingContentPage');
    this.common.writeToBehaviorLog(constant.SCREEN_PLAYING_CONTENT_CODE, constant.MSG_BEHAVIOR_BLOG);
    var url = await this.common.getContentFile(this.contenCode, 'main/main.html');
    if (url) {
      var param = this.generateEncodedUrl();
      var param2 = this.datePipe.transform(Date.now(), 'hhmmss');
      url = url + '?' + String(param) + '&currentTime=' + param2;
      this.urlContent = this.sanitizer.bypassSecurityTrustResourceUrl(String(url));
    } else {
      this.urlContent = url;
    }
    if (this.listContents) {
      this.currentIndex = this.common.findWithAttr(this.listContents, 'Id', this.contentId);
      if (this.currentIndex == (this.listContents.length - 1)) {
        this.disableButtonNext = true;
      }
    }
    else {
      this.disableButtonNext = true;
    }
    // handle event back button to chat mode screen
    this.navBar.backButtonClick = async (e: UIEvent) => {
      await this.updateHistories();
      this.navCtrl.pop();
    };

  }

  /**
  * go to next page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async nextPage() {

    await this.updateHistories();
    if (this.currentIndex < (this.listContents.length - 1)) {
      this.currentIndex = this.currentIndex + 1;
      var url = await this.common.getContentFile(this.listContents[this.currentIndex].ContentCode, 'main/main.html');
      if (url) {
        this.contenCode = this.listContents[this.currentIndex].ContentCode;
        this.contentId = this.listContents[this.currentIndex].Id;
        this.categoryId = this.listContents[this.currentIndex].CategoryId;
        var param = this.generateEncodedUrl();
        var param2 = this.datePipe.transform(Date.now(), 'hhmmss');
        url = url + '?' + String(param) + '&currentTime=' + param2;;
        this.urlContent = this.sanitizer.bypassSecurityTrustResourceUrl(String(url));
        this.contenName = this.listContents[this.currentIndex].ContentName;
      } else {
        this.urlContent = url;
      }
    }
    if (this.currentIndex == (this.listContents.length - 1)) {
      this.disableButtonNext = true;
    }

  }

  /**
  * update history
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async updateHistories() {

    var arrayScore;
    var resultElement = document.getElementsByTagName('iframe')[1].contentDocument.getElementById('result');
    if (resultElement) {
      arrayScore = resultElement.getElementsByTagName('input');
      if (arrayScore.length > 0) {
        for (let result of arrayScore) {
          var currenContencode = result.getAttribute('contentcode');
          var currentUserID = result.getAttribute('userid');
          var currentScore = result.getAttribute('score');
          var currentTime = result.getAttribute('time');
          var transferResult = {
            ContentCode: currenContencode,
            UserId: currentUserID,
            Score: currentScore,
            Time: currentTime,
          }
          await this.commitHistoriesApi(transferResult);
        }
      }
    }

  }

  /**
  * commit update history
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async commitHistoriesApi(result) {

    let data = {
      ContentId: this.contentId,
      ContentCode: result.ContentCode,
      ContentName: this.contenName,
      StaffId: this.tokenObj.userObject.Id,
      UserId: result.UserId,
      CategoryId: this.categoryId,
      Score: result.Score,
      Time: result.Time
    };
    await this.http.post(constant.API_HISTORIES, JSON.stringify(data), this.tokenObj.options).toPromise().catch(
      err => {
        console.log(err);
      });

  }

  /**
  * generate encode url
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  generateEncodedUrl() {

    var userid01, userid02, userid03, userid04, userid05, userid06;
    var username01, username02, username03, username04, username05, username06;

    for (let people of this.arrayPeople) {
      switch (people.pos) {
        case 1:
          userid01 = (people.id) ? people.id : -1;
          username01 = (people.name) ? people.name : '';
          break;
        case 2:
          userid02 = (people.id) ? people.id : -1;
          username02 = (people.name) ? people.name : '';
          break;
        case 3:
          userid03 = (people.id) ? people.id : -1;
          username03 = (people.name) ? people.name : '';
          break;
        case 4:
          userid04 = (people.id) ? people.id : -1;
          username04 = (people.name) ? people.name : '';
          break;
        case 5:
          userid05 = (people.id) ? people.id : -1;
          username05 = (people.name) ? people.name : '';
          break;
        case 6:
          userid06 = (people.id) ? people.id : -1;
          username06 = (people.name) ? people.name : '';
          break;
      }
    }
    let paramsUrl = `XX0000=${this.contenCode}` +
      `&userid01=${userid01}&username01=${username01}` +
      `&userid02=${userid02}&username02=${username02}` +
      `&userid03=${userid03}&username03=${username03}` +
      `&userid04=${userid04}&username04=${username04}` +
      `&userid05=${userid05}&username05=${username05}` +
      `&userid06=${userid06}&username06=${username06}`;

    return encodeURI(paramsUrl);

  }

}



