import { Component } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular/index';

/**
 * Generated class for the StaffPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'staff-popover',
  templateUrl: 'staff-popover.html'
})
export class StaffPopoverComponent {

  callback: any;
  searchQuery: string = '';
  items: string[];
  arrayItem: any;
  itemselect: any;

  constructor(private params: NavParams, private viewCtrl: ViewController) {

    this.callback = this.params.get('cbbStaffs');
    this.itemselect = this.params.get('itemSelect');
    this.arrayItem = this.callback.items;

  }

  /**
  * search function
  *    param: 
  *      -ev:any
  *    return: 
  *      -N/A
  */
  getItems(ev: any) {

    // Reset items back to all of the items
    // set val to the value of the searchbar
    let val = ev.target.value;
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }

  }

  /**
  * go to detail screen
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  goToDetails() {

    let returnArray: any
    this.arrayItem.forEach(element => {
      if (element.Id == this.itemselect) {
        returnArray = element;
      }
    });
    this.viewCtrl.dismiss(returnArray);

  }

  /**
  * action cancel button
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss();

  }

}



