import { Component, ViewChild } from '@angular/core';
import { ViewController } from 'ionic-angular';
import { DatePickerDirective } from 'ion-datepicker';
import { Events } from 'ionic-angular';
import { NavParams } from 'ionic-angular/index';
import { commonClass } from './../../app/common';
import { constant } from '../../app/constant';

/**
 * Generated class for the SpecifyPeriodPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'specify-period-popover',
  templateUrl: 'specify-period-popover.html'
})
export class SpecifyPeriodPopoverComponent {

  @ViewChild(DatePickerDirective) public datepicker: DatePickerDirective;
  // Declare Variable
  callEvent: Events;
  endDate: Date = new Date();
  startDate: Date = new Date(new Date().setDate(this.endDate.getDate() - 30));
  eventName: String = null;

  constructor(private params: NavParams,
    public events: Events,
    public viewCtr: ViewController,
    public common: commonClass) {

    this.callEvent = events;
    this.endDate = this.params.get(constant.END_DATE);
    this.startDate = this.params.get(constant.START_DATE);
    this.eventName = this.params.get(constant.EVENT_NAME);

  }

  /**
   * Event change periode
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  changePeriod() {

    if (this.startDate > this.endDate) {
      this.common.showModalDialogConfirm(constant.NWK_CK_006, true);
    } else {
      this.viewCtr.dismiss();
      this.callEvent.publish(String(this.eventName), this.startDate, this.endDate);
    }

  }

  /**
   * Close popup
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  closePopup() {

    this.viewCtr.dismiss();

  }

}



