import { Component } from '@angular/core';
// import { NavController, ViewController, NavParams } from 'ionic-angular/index';
import { ViewController, NavParams } from 'ionic-angular';

/**
 * Generated class for the StaffPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
    template: `
    <ion-list radio-group [(ngModel)]="level">
    <div>
    <ion-item style="padding-right:16px;">
      <ion-label>
        <ion-icon style="color: #74cb37;font-size: 25px;"  ios="ios-heart" md="md-heart"></ion-icon>
      </ion-label>
      <ion-radio (ionSelect)="close()" value="1"></ion-radio>
    </ion-item>
    <ion-item style="padding-right:16px;">
    <ion-label>
    <ion-icon style="color: #74cb37;font-size: 25px;"  ios="ios-heart" md="md-heart"></ion-icon>
    <ion-icon style="color: #74cb37;font-size: 25px;"  ios="ios-heart" md="md-heart"></ion-icon>
    </ion-label>
      <ion-radio (ionSelect)="close()" value="2"></ion-radio>
    </ion-item>
    <ion-item style="border-bottom-right-radius: 10px;border-bottom-left-radius:10px;padding-right:16px;">
    <ion-label>
    <ion-icon style="color: #74cb37;font-size: 25px;"  ios="ios-heart" md="md-heart"></ion-icon>
    <ion-icon style="color: #74cb37;font-size: 25px;"  ios="ios-heart" md="md-heart"></ion-icon>
    <ion-icon style="color: #74cb37;font-size: 25px;"  ios="ios-heart" md="md-heart"></ion-icon>
    </ion-label>
      <ion-radio (ionSelect)="close()" value="3"></ion-radio>
    </ion-item>
    </div>
  </ion-list>
    `
})
export class levelPopoverPage {
    level : string;
    constructor(public viewCtrl: ViewController,public navParams:NavParams) {
        this.level = this.navParams.get('level');
     }

    close() {
        console.log(this.level);
        this.viewCtrl.dismiss(this.level);
    }
}


