import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { PopoverController, Popover } from 'ionic-angular';
import { StaffPopoverComponent } from './../../components/staff-popover/staff-popover';
import { FacilityPopoverComponent } from './../../components/facility-popover/facility-popover';
import { UserSortingOrderPopoverComponent } from './../../components/user-sorting-order-popover/user-sorting-order-popover';
import { Events } from 'ionic-angular';
import { RegisterUserComponent } from './../../components/Register-User/Register-User';
import { ModalController } from 'ionic-angular';
import { Http } from '@angular/http';
import { JwtHelper } from 'angular2-jwt';
import { AlertController } from 'ionic-angular';
import { commonClass } from './../../app/common';
import { constant } from './../../app/constant';

interface User {
  Id: string,
  username: string,
  userKana: string,
  birthDaty: any,
  Sex: string
  Age: Number,
  FacilityName: string,
  StaffName: string,
  LastPlayed: string,
  Level: string,
  ChallengeScore: string,
  PhotoURL: string,
  isUsed: boolean,
  jpDay: string

}
@Component({
  selector: 'page-user-list',
  templateUrl: 'user-list.html',
})
export class UserListPage {

  public jwtHelper: JwtHelper = new JwtHelper();
  callEvent: Events;
  imgList: Array<User>;
  arraySearch: any;                                        // search array
  arrayItem: any;                                          // facilities array
  arrayStaffItem: any;                                     // staff array
  idSort: any;                                             // id sort return
  labelFacilities: any;                                    // label combobox Facilities
  labelIdSort: any                                         // label combobox sort
  idFacilities: any;
  public alertPresented: any;                                    // id facilities return
  idStaff: any;                                            // id staff return
  labelStaff: any;                                         // label combobox staff
  userObject: { options: any, userObject: any };
  temp: Date;
  idBefore: any
  searchWord: string;
  popover: Popover;

  constructor(public common: commonClass,
    private alertController: AlertController,
    public navCtrl: NavController,
    private http: Http,
    public navParams: NavParams,
    public popoverCtrl: PopoverController,
    public modalCtrl: ModalController,
    public events: Events,
    public loadingCtrl: LoadingController) {

    this.callEvent = events;
    this.imgList = [];
    this.labelIdSort = constant.SORT_TEXT_USERLIST;
    this.labelFacilities = constant.TEXT_FACILITIES;
    this.labelStaff = constant.TEXT_FACILITIES;
    this.userObject = this.common.tokenObj;

  }

  /**
  * load combobox staff
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async loadFacilities() {

    var data = await this.http.get(constant.API_FACILITIES_GET + "?AgreementId=" + this.userObject.userObject.AgreementId, this.userObject.options)
      .toPromise().catch(async error => {
        // if error show meseage       
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
      });
    if (data) {
      this.arrayItem = data.json();
    }

  }

  /**
   * load combobox staff
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  async loadStaffsList() {

    var data = await this.http.get(constant.API_STAFFS_GET + "?AgreementId=" + this.userObject.userObject.AgreementId, this.userObject.options).toPromise()
      .catch(async error => {
        // if error show meseage       
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
      });
    if (data) {
      this.arrayStaffItem = data.json();
    }

  }

  /**
  * load content userlist
  *    param: 
  *      -facilities: any
  *      -staff: any
  *      - idSort: any
  *    return: 
  *      -N/A
 */
  async loadContent(facilities: any, staff: any, idSort: any) {

    if (this.idStaff == null) {
      // if staff == null 
      staff = "";
      // set label facilities combobox = "サウンドパネル"      
      this.labelStaff = constant.TEXT_FACILITIES;
    }
    if (this.idFacilities == null) {
      // if staff == null        
      facilities = "";
      // set label staff combobox = "未設定"  
      this.labelFacilities = constant.TEXT_FACILITIES;
    }
    if (this.idSort == null) {
      // if staff == null 
      idSort = 1;
      // set label facilities combobox = "未設定"  
      this.labelIdSort = constant.SORT_TEXT_USERLIST;
    }
    this.imgList = [];
    this.arraySearch = [];
    // call api 
    var data;
    data = await this.http.get(constant.API_SEARCHUSER_GET +
      "?SortId=" + idSort +
      "&AgreementId=" + this.userObject.userObject.AgreementId +
      "&StaffId=" + staff +
      "&FacilityId=" + facilities + "",
      this.userObject.options).toPromise()
      .catch(async error => {
        await this.common.showModalDialogConfirm(constant.NWK_CK_005, false);
      });
    if (data) {
      data = data.json();
      let sex: string;
      let level: string;
      for (let i = 0; i < data.items.length; i++) {
        let item = data.items[i];
        if (item.Sex == 1) {
          sex = constant.SEX1_TEXT;
        }
        else if (item.Sex == 2) {
          sex = constant.SEX2_TEXT;
        }
        else if (item.Sex == 3) {
          sex = constant.SEX3_TEXT;
        }
        level = item.Level;
        let age = this.common.calculateAge(new Date(item.Birthday))
        var convertBirthday = this.common.convertToJpDate(item.Birthday);
        let people: User = {
          Id: item.Id,
          username: item.UserName,
          userKana: item.UserKana,
          birthDaty: convertBirthday,
          Sex: sex,
          Age: age,
          FacilityName: item.FacilityName,
          StaffName: item.StaffName,
          LastPlayed: item.LatestPlay,
          Level: level,
          ChallengeScore: item.ChallengeScore,
          PhotoURL: item.PhotoURL,
          isUsed: !!item.LatestPlay,
          jpDay: item.LatestPlay ? this.common.getJpDay(item.LatestPlay) + 'にプレイ' : ''
        };
        this.imgList.push(people);
        this.arraySearch.push(people);
      }
    }

  }

  /**
  * search function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  searchTap() {

    let val = this.searchWord;
    if (val && val.trim() != '') {
      this.arraySearch = this.imgList.filter((item) => {
        return (item.username.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.imgList;
    }
    if (this.arraySearch.length == 0) {
      this.present("", constant.ERROR_MESSAGE_SEARCHBAR_USERLIST)
    }

  }

  async ionViewDidLoad() {

    this.common.writeToBehaviorLog(constant.SCREEN_USERLIST_CODE, constant.MSG_BEHAVIOR_BLOG);   // write to behavior log
    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    loading.present();
    var check = await this.common.checkAccount();
    if (check) {
      // load content                                          
      this.loadContent("", "", "1");
      loading.dismiss();
      // load combobox facilities
      await this.loadFacilities();
      // load combobox staff                               
      await this.loadStaffsList();
    }

  }

  ionViewWillEnter() {

    this.events.subscribe(constant.BACK_TO_USER_LIST, () => {
      this.loadContent('', '', '1');
    });

  }

  ionViewWillUnload() {

    this.popover && this.popover.dismiss();

  }

  /**
  * present staff pop up
  *    param: 
  *      -ev:event
  *    return: 
  *      -N/A
 */
  presentPopoverStaff(ev) {

    this.common.writeToBehaviorLog(constant.SCREEN_STAFF_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.popover = this.popoverCtrl.create(StaffPopoverComponent, { cbbStaffs: this.arrayStaffItem, itemSelect: this.idStaff });
    this.popover.present({
      ev: ev
    });
    this.popover.onDidDismiss(data => {
      if (data == "") {
        this.labelStaff = constant.TEXT_FACILITIES
        this.idStaff = ''
        this.loadContent(this.idFacilities, this.idStaff, this.idSort)
      }
      else {                                // dismiss pop up 
        this.labelStaff = data.StaffName;                               // set label staff base for current user choice
        this.idStaff = data.Id                                          // send back id staff
        this.loadContent(this.idFacilities, this.idStaff, this.idSort)      // reload content
      }
    });

  }

  /**
  * present facility pop up
  *    param: 
  *      -ev:event
  *    return: 
  *      -N/A
 */
  presentPopoverFacility(ev) {

    this.common.writeToBehaviorLog(constant.SCREEN_FACILITIES_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.popover = this.popoverCtrl.create(FacilityPopoverComponent, { cbbFacilities: this.arrayItem, itemSelect: this.idFacilities });
    this.popover.present({
      ev: ev
    });
    this.popover.onDidDismiss(data => {                                    // dismiss pop up
      if (data == "") {
        this.labelFacilities = constant.TEXT_FACILITIES
        this.idFacilities = ''
        this.loadContent(this.idFacilities, this.idStaff, this.idSort)
      }
      else {
        (data.FacilityName.length > 10) ? this.labelFacilities = data.FacilityName.substr(0, 5) + '...' : this.labelFacilities = data.FacilityName;
        this.idFacilities = data.Id;
        this.loadContent(this.idFacilities, this.idStaff, this.idSort)
      }
    });

  }

  /**
  * present user sort pop up
  *    param: 
  *      -ev:event
  *    return: 
  *      -N/A
 */
  presentPopoverUserSort(ev) {

    this.common.writeToBehaviorLog(constant.SCREEN_SORT_USER_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.popover = this.popoverCtrl.create(UserSortingOrderPopoverComponent, { itemSelect: this.idSort });
    this.popover.present({
      ev: ev
    });
    this.popover.onDidDismiss(data => {                               // dismiss pop up           
      if (data == 1) {                                              // if id == 1
        this.labelIdSort = constant.SORT_TEXT_USERLIST;
        this.idBefore = data;                                // set text combobox sort = "新着順"
      }
      else if (data == 2) {                                          // if id == 2
        this.labelIdSort = constant.TEXT_SORT2;
        this.idBefore = data;                                // set text combobox sort = "人気順"
      }
      else if (data == 3) {                                                        // if id == 3 
        this.labelIdSort = constant.TEXT_SORT3;
        this.idBefore = data;                             // set text combobox sort = "人気順"
      }
      if (data == null) {
        data = this.idBefore;
        if (data == 1) {                                              // if id == 1
          this.labelIdSort = constant.SORT_TEXT_USERLIST;
          this.idBefore = data;                                // set text combobox sort = "新着順"
        }
        else if (data == 2) {                                          // if id == 2
          this.labelIdSort = constant.TEXT_SORT2;
          this.idBefore = data;                                // set text combobox sort = "人気順"
        }
        else if (data == 3) {                                                        // if id == 3 
          this.labelIdSort = constant.TEXT_SORT3;
          this.idBefore = data;                         // set text combobox sort = "人気順"
        }
      }
      this.idSort = data;
      this.loadContent(this.idFacilities, this.idStaff, this.idSort);    // reload content
    });

  }

  /**
  * go to detail user
  *    param: 
  *      -item:object
  *    return: 
  *      -N/A
 */
  goToUserDetailPage(item) {

    this.callEvent.publish(constant.EVENT_USER_DETAIL, item);

  }

  /**
  * go to noAH History Page
  *    param: 
  *      -item:object 
  *    return: 
  *      -N/A
 */
  goToNoAHHistoryPage(item) {

    this.callEvent.publish(constant.EVENT_NOAH_PLAYING, item);

  }

  /**
  * open specifying period screen
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  showUser() {

    let specifyingPeriod = this.modalCtrl.create(RegisterUserComponent, {}, { enableBackdropDismiss: false });
    specifyingPeriod.present();

  }

  /**
  * function search
  *    param: 
  *      -ev: any
  *    return: 
  *      -N/A
 */
  getItems(ev: any) {

    let val = ev.target.value;
    if (val && val.trim() != '') {
      this.arraySearch = this.imgList.filter((item) => {
        return (item.username.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.imgList;
    }
    if (this.arraySearch.length == 0) {
      this.present("", constant.ERROR_MESSAGE_SEARCHBAR_USERLIST);
    }

  }

  /**
  * present alert dialogue
  *    param: 
  *      -title: string
         -subtitle: string 
  *    return: 
  *      -N/A
 */
  present(title, subTitle) {

    let vm = this
    if (!vm.alertPresented) {
      vm.alertPresented = true
      vm.alertController.create({
        title: title,
        subTitle: subTitle,
        buttons: [{
          text: constant.BUTTON_OK,
          handler: () => {
            vm.alertPresented = false
          }
        }],
      }).present();
    }

  }

  /**
  * return Login
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  returnLogin() {

    this.callEvent.publish(constant.EVENT_CHANGE_LOGIN_PAGE);

  }

}


