import { Component } from '@angular/core';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { ViewController, Events, NavController, AlertController, LoadingController, NavParams, PopoverController, Popover } from 'ionic-angular';
import { Http } from '@angular/http';
import { JwtHelper } from 'angular2-jwt';
import { commonClass } from '../../app/common';
import { constant } from '../../app/constant';
import { levelPopoverPage } from './../../components/levelpopover';

@Component({
  selector: 'updating-user',
  templateUrl: 'updating-user.html'
})
export class UpdatingUserComponent {

  jwtHelper: JwtHelper = new JwtHelper();
  facilities: any;
  staffs: any;
  Image: string;
  binaryImg: string;
  newUser: any;
  dayNew: any;
  dayOld: any;
  monthNew: any;
  monthOld: any;
  yearNew: any;
  yearOld: any;
  level: string;
  popover: Popover;
  user: any;
  tokenObj: any;
  agreementId: string;

  constructor(params: NavParams,
    public viewCtrl: ViewController,
    public popoverCtrl: PopoverController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    public camera: Camera,
    private http: Http,
    private common: commonClass,
    private events: Events) {

    this.user = params.get('user') || {};
    if (this.user) {
      this.newUser = { ...this.user };
      this.Image = this.user.PhotoURL;
      if (this.Image == "") {
        this.Image = constant.USER_DEFAULT_IMAGE
      }
      this.user.Birthday = new Date(this.newUser.Birthday);
      this.splitBirthdayOfUser();
      console.log(this.user);
    }

    // this.common.getToken().then(tokenObj => {
    //   this.tokenObj = tokenObj;
    //   this.agreementId = this.tokenObj.userObject.AgreementId;
    //   this.loadFacilities();
    //   this.loadStaffsList();
    // });
    this.tokenObj = this.common.tokenObj;
    this.agreementId = this.tokenObj.userObject.AgreementId;
    this.loadFacilities();
    this.loadStaffsList();

  }

  /**
  * split bỉthday of user
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  splitBirthdayOfUser() {

    let birthday = new Date(this.newUser.Birthday);
    this.yearNew = birthday.getFullYear();
    let month = birthday.getMonth() + 1;
    this.monthNew = month > 9 ? month : '0' + month;
    let date = birthday.getDate()
    this.dayNew = date > 9 ? date : '0' + date;
    this.dayOld = this.dayNew;
    this.monthOld = this.monthNew;
    this.yearOld = this.yearNew;

  }

  /**
  * take picture from library 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  takePicfromLibrary() {

    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: 0,
      sourceType: 0
    };
    this.takePic(options);

  }

  /**
  * take picture from camera  
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  takePicfromCamera() {

    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: 0,
      sourceType: 1,
      cameraDirection: this.camera.Direction.FRONT
    };
    this.takePic(options);

  }

  /**
  * get picture from camera  
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async takePic(option) {

    await this.camera.getPicture(option).then((imageData) => {
      let image = constant.SETTING_IMAGE_CAMERA + imageData;
      this.binaryImg = imageData;
      this.Image = image;
    }, (err) => {
    });

  }

  /**
  * get facilities combobox  
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async loadFacilities() {

    // this.http.get(`${constant.API_FACILITIES_GET}?AgreementId=${this.agreementId}`, this.tokenObj.options)
    //   .map(res => res.json())
    //   .subscribe(data => {
    //     this.facilities = data.items;
    //   }, (error) => {
    //     let alert = this.alertCtrl.create({
    //       subTitle: constant.NWK_CK_065,
    //       buttons: [constant.BUTTON_OK]
    //     });
    //     alert.present();
    //   });
    var data = await this.http.get(constant.API_FACILITIES_GET + "?AgreementId=" + this.agreementId, this.tokenObj.options).toPromise().catch(async error => {
      // if error show meseage       
      await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
    });
    if (data) {
      this.facilities = data.json().items;
    }

  }

  /**
  * get staff combobox  
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async loadStaffsList() {

    // this.http.get(`${constant.API_STAFFS_GET}?AgreementId=${this.agreementId}`, this.tokenObj.options)
    //   .map(res => res.json())
    //   .subscribe(data => {
    //     this.staffs = data.items;
    //   }, (error) => {
    //     let alert = this.alertCtrl.create({
    //       subTitle: constant.NWK_CK_065,
    //       buttons: [constant.BUTTON_OK]
    //     });
    //     alert.present();
    //   });
    var data = await this.http.get(constant.API_STAFFS_GET + "?AgreementId=" + this.agreementId, this.tokenObj.options).toPromise().catch(async error => {
      // if error show meseage       
      await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
    });
    if (data) {
      this.staffs = data.json().items;
    }

  }

  /**
  * update user  
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  update() {

    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    loading.present();
    this.newUser.Birthday = `${this.yearNew}/${this.monthNew}/${this.dayNew}`;
    console.log(this.newUser);
    if (!this.newUser.UserKana
      || !this.newUser.UserName || !this.newUser.Sex
      || !this.newUser.FacilityId || !this.newUser.StaffId || !this.newUser.Level) {

      let mes = '';

      if (!this.newUser.UserKana) mes += constant.USER_KANA_REGISTER_SCREEN;
      if (!this.newUser.UserName) mes += constant.USER_NAME_REGISTER_SCREEN;
      if (!this.newUser.Sex) mes += constant.USER_KANA_REGISTER_SCREEN;
      if (!this.newUser.year || !this.newUser.month || !this.newUser.day) mes += constant.BIRTHDAY_REGISTER_SCREEN;
      if (!this.newUser.FacilityId) mes += constant.FACILITY_ID_REGISTER_SCREEN;
      if (!this.newUser.StaffId) mes += constant.STAFF_ID_REGISTER_SCREEN;
      if (!this.newUser.Level) mes += constant.LEVEL_REGISTER_SCREEN;
      if (mes.length > 0) mes = mes.substring(0, mes.length - 1);

      mes = constant.NWK_CK_001.replace('{0}', mes);
      let alert = this.alertCtrl.create({
        subTitle: mes,
        buttons: [constant.BUTTON_OK]
      });
      loading.dismiss();
      alert.present();
    }
    else if (this.newUser.UserName.length > 10 || this.common.checkIsJapaneseChar(this.newUser.UserName) == false) {
      let mes = '氏名';
      mes = constant.NWK_CK_015.replace('{0}', mes);
      let alert = this.alertCtrl.create({
        subTitle: mes,
        buttons: [constant.BUTTON_OK]
      });
      loading.dismiss();
      alert.present();
    }
    else if (this.newUser.UserKana.length > 15 || this.common.checkIsHiraganaChar(this.newUser.UserKana) == false) {
      let mes = 'かな';
      mes = constant.NWK_CK_016.replace('{0}', mes);
      let alert = this.alertCtrl.create({
        subTitle: mes,
        buttons: ['OK']
      });
      loading.dismiss();
      alert.present();
    }
    else if (this.binaryImg) {
      this.http.post(constant.API_UPLOAD_USER, JSON.stringify({ UserId: this.newUser.Id, Binary: this.binaryImg }), this.tokenObj.options)
        .map(res => res.json())
        .subscribe(data => {

          this.newUser.PhotoURL = data.photourl;
          this.http.put(constant.API_UPDATE_USER, JSON.stringify(this.newUser), this.tokenObj.options)
            .subscribe(data => {
              loading.dismiss();
              this.events.publish(constant.BACK_TO_USER_LIST);
              this.goBackafterUpdate();
            }, error => {
              let alert = this.alertCtrl.create({
                subTitle: error,
                buttons: [constant.BUTTON_OK]
              });
              loading.dismiss();
              alert.present();
            });

        }, error => {
          let alert = this.alertCtrl.create({
            subTitle: error,
            buttons: [constant.BUTTON_OK]
          });
          loading.dismiss();
          alert.present();
        });
    }
    else {
      this.http.put(constant.API_UPDATE_USER, JSON.stringify(this.newUser), this.tokenObj.options)
        .subscribe(data => {
          loading.dismiss();
          this.events.publish(constant.BACK_TO_USER_LIST);
          this.goBackafterUpdate();
        }, error => {
          let alert = this.alertCtrl.create({
            subTitle: constant.NWK_CK_014,
            buttons: [constant.BUTTON_OK]
          });
          loading.dismiss();
          alert.present();
        });
    }

  }

  /**
  * go back after update   
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  goBackafterUpdate() {

    this.viewCtrl.dismiss('1');

  }

  goBack() {

    if (this.yearNew != this.yearOld || this.monthOld != this.monthNew || this.dayOld != this.dayNew ||
      this.newUser.UserKana != this.user.UserKana || this.newUser.UserName != this.user.UserName ||
      this.newUser.Sex != this.user.Sex || this.newUser.FacilityId != this.user.FacilityId ||
      this.newUser.StaffId != this.user.StaffId || this.newUser.Level != this.user.Level) {
      console.log(this.newUser, this.user);
      let alert = this.alertCtrl.create({
        subTitle: constant.NWK_CK_017,
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            handler: () => {

            }
          },
          {
            text: 'OK',
            handler: () => {

              this.viewCtrl.dismiss('0');
            }
          }
        ]
      });
      alert.present();
    }
    else {
      this.viewCtrl.dismiss('0');
    }

  }

  /**
  * present level combobox  
  *    param: 
  *      -event:event
  *    return: 
  *      -N/A
  */
  levelPopover(event) {

    this.popover = this.popoverCtrl.create(levelPopoverPage, { level: this.newUser.Level });
    this.popover.present({
      ev: event
    });
    this.popover.onDidDismiss(data => {
      console.log(data);
      if (data) {
        this.newUser.Level = parseInt(data);
        console.log(this.newUser);
      }
    });
    
  }

}



