import { Events } from 'ionic-angular';
import { NativeStorage } from '@ionic-native/native-storage';
import { Http, Headers, RequestOptions } from '@angular/http';
import { JwtHelper } from 'angular2-jwt';
import { AlertController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { constant } from './../app/constant';
import { File } from '@ionic-native/file';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { Zip } from '@ionic-native/zip';

@Injectable()
export class commonClass {

  public jwtHelper: JwtHelper = new JwtHelper();
  callEvent: Events;
  public tokenObj: { options: any, userObject: any };
  public endDate: Date = new Date();
  public startDate: Date = new Date(new Date().setDate(this.endDate.getDate() - 30));
  public accountRemainingDays: number = null;
  public listFile: any;
  public listFileOfContent: any;
  fileTransfer: FileTransferObject = this.transfer.create();

  /**
   * Constructor of common class
   *    param: 
   *      -nativeStorage
   *      -events
   *      -http
   *      -alertController
   *    return: 
   *      -N/A
  */
  constructor(public nativeStorage: NativeStorage,
    public events: Events,
    private http: Http,
    private alertController: AlertController,
    private file: File,
    private zip: Zip,
    private transfer: FileTransfer, ) {

    this.callEvent = events;

  }

  /**
   * Get token of user
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  public async getToken() {

    var resultObject: { options: any, userObject: any };
    var token = await this.loadValueLocalStorage('token');
    if (token != null) {
      let headers = new Headers({
        "Authorization": "Bearer " + token,
        'Content-Type': 'application/json'
      });
      let options = new RequestOptions({
        headers: headers
      });
      resultObject = { options: options, userObject: this.jwtHelper.decodeToken(String(token)) };
    } else {
      resultObject = null;
    }
    return resultObject;

  }

  /**
   * Get value of attribute from local storage
   *    param: 
   *      -attributeName: attribute name
   *    return: 
   *      -value
  */
  public async loadValueLocalStorage(attributeName) {

    var result = null;
    result = await this.nativeStorage.getItem(attributeName).catch(
      function () {
        result = null;
      }
    );
    return result;

  }

  /**
   * Save value of attribute to local storage
   *    param: 
   *      -itemName: attribute name
   *      -value: value of attribute
   *    return: 
   *      -true: success/ false: fail
  */
  public async saveValueLocalStorage(itemName, value) {

    var result = false;
    await this.nativeStorage.setItem(itemName, value).then(
      data => {
        if (data) {
          result = true;
        }
      }
    ).catch(
      error => {
        result = false;
      }
      );
    return result;

  }

  /**
   * Check current login account
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  public async checkAccount() {

    var result = true;
    var accountcheck;
    if (this.tokenObj != null) {
      accountcheck = await this.http.post(constant.API_ACCOUNT_CHECK,
        JSON.stringify({ AgreementId: this.tokenObj.userObject.AgreementId, UserId: this.tokenObj.userObject.Id }),
        this.tokenObj.options).toPromise().catch(
        error => {
          console.log(error);
          this.returnLogin();
          result = false;
        }
        );
      if (accountcheck) {
        let jsonCallBack = accountcheck.json();
        if (jsonCallBack.Attribute == 0) {
          if (jsonCallBack.RemainingDays > 0) {
            this.accountRemainingDays = jsonCallBack.RemainingDays;
          }
          else {
            this.returnLogin();
            result = false;
          }
        }
        else if (jsonCallBack.Attribute == 1) {
          if (!jsonCallBack.Available) {
            this.returnLogin();
            result = false;
          } else {
            this.accountRemainingDays = null;
          }
        }
        else {
          this.returnLogin();
          result = false;
        }
      }
    } else {
      this.returnLogin();
      result = false;
    }
    return result;

  }

  /**
   * Format date in yyyy/mm/dd
   *    param: 
   *      -date: Date for format
   *    return: 
   *      -date after format
  */
  public formatDate(date) {

    var d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;
    return [year, month, day].join('/');

  }

  /**
   * Format date in japanese
   *    param: 
   *      -date: Date for format
   *      -isYearJP: flag convert to Jp calendar or not
   *    return: 
   *      -date after format
  */
  public formatDate2Jp(date, isYearJP) {

    var d = new Date(date);
    var month = '' + (d.getMonth() + 1);
    var day = '' + d.getDate();
    var year;
    year = d.getFullYear();
    var subDate = d.getDay();
    var newSubDate = '';

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;
    if (!isYearJP) {
      switch (subDate) {
        case 1:
          newSubDate = '（月）';
          break;
        case 2:
          newSubDate = '（火）';
          break;
        case 3:
          newSubDate = '（水）';
          break;
        case 4:
          newSubDate = '（木）';
          break;
        case 5:
          newSubDate = '（金）';
          break;
        case 6:
          newSubDate = '（土）';
          break;
        case 0:
          newSubDate = '（日）';
      }
    } else {
      var dateJPCal = d.toLocaleDateString("ja-JP-u-ca-japanese");
      console.log("---------------------", dateJPCal)
      year = dateJPCal.split('/').shift();

    }

    return year + '年' + month + '月' + day + '日' + newSubDate;

  }

  public convertToJpDate(date) {

    var d = new Date(date);
    var month = '' + (d.getMonth() + 1);
    var day = '' + d.getDate();
    var year = constant.JAPANESE_CALENDAR_CHART[`${d.getFullYear()}`];

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return year + '年' + month + '月' + day + '日';
  }

  public getJpDay(date) {

    var d = new Date(date);
    var subDate = d.getDay();
    var newSubDate = '';
    switch (subDate) {
      case 1:
        newSubDate = '（月）';
        break;
      case 2:
        newSubDate = '（火）';
        break;
      case 3:
        newSubDate = '（水）';
        break;
      case 4:
        newSubDate = '（木）';
        break;
      case 5:
        newSubDate = '（金）';
        break;
      case 6:
        newSubDate = '（土）';
        break;
      case 0:
        newSubDate = '（日）';
    }
    return newSubDate;
  }

  /**
   * Return back to login page
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  public returnLogin() {

    this.callEvent.publish('changeLoginPage');

  }

  /**
   * Find index in array with value of attribute
   *    param: 
   *      -array: Array for find index
   *      -attr: Attribute will be find in array
   *      -value: The valur for finding
   *    return: 
   *      -index of map item in array
  */
  public findWithAttr(array, attr, value) {

    for (var i = 0; i < array.length; i += 1) {
      if (array[i][attr] === value) {
        return i;
      }
    }
    return -1;

  }

  /**
   * Find index in array with value of attribute
   *    param: 
   *      -ScreenId: Id of screen
   *      -Processing: proccessing.
   *    return: 
   *      -N/A
  */
  async writeToBehaviorLog(ScreenId: string, Processing: string) {

    var result = true;
    if (this.tokenObj != null) {
      await this.http.post(constant.API_BEHAVIOR_LOG,
        JSON.stringify({ StaffId: this.tokenObj.userObject.Id, ScreenId: ScreenId, Processing: Processing }), this.tokenObj.options).toPromise()
        .catch(error => {
          console.log(error);
          result = false;
        });
    }
    return result;

  }

  /**
   * Show modal Ask
   *    param: 
   *      -message: message dialog
   *    return: 
   *      -N/A
  */
  showModalDialogAsk(message) {

    return new Promise((resolve, reject) => {
      let confirmDialog = this.alertController.create({
        subTitle: message,
        buttons: [
          {
            text: constant.BUTTON_CANCEL,
            handler: () => {
              resolve(false);
            }
          },
          {
            text: constant.BUTTON_OK,
            handler: () => {
              resolve(true);
            }
          }
        ],
        enableBackdropDismiss: false
      });
      confirmDialog.present();
    })

  }

  /**
   * Show modal Ask
   *    param: 
   *      -message: message dialog
   *      -isDisableOutside: enableBackdropDismiss flag
   *    return: 
   *      -N/A
  */
  showModalDialogConfirm(message, isDisableOutside) {

    return new Promise((resolve, reject) => {
      let confirmDialog = this.alertController.create({
        subTitle: message,
        buttons: [
          {
            text: constant.BUTTON_OK,
            handler: () => {
              resolve(true);
            }
          }
        ],
        enableBackdropDismiss: isDisableOutside
      });
      if (confirmDialog != null) {
        confirmDialog.present();
      }
      else {
        confirmDialog.dismiss()
      }
    })


  }

  calculateAge(birthday) { // birthday is a date
    var ageDifMs = Date.now() - birthday.getTime();
    var ageDate = new Date(ageDifMs); // miliseconds from epoch
    return Math.abs(ageDate.getUTCFullYear() - 1970);
  };

  encodeURINonASCII(str) {

    var list_character = str.split("");
    var result = '';
    for (var i = 0; i < list_character.length; i++) {
      var charcode = list_character[i].charCodeAt(0);
      // If character is not ASCII
      if (!(charcode >= 32 && charcode <= 126)) {
        result = result + encodeURI(list_character[i]);
      }
      else {
        result = result + list_character[i];
      }
    }
    return result;

  }

  checkIsASCII(str) {
    return /^[\x00-\x7F]*$/.test(str);
  }

  checkIsJapaneseChar(str) {

    return /^([ァ-ヶーぁ-ん一-龠ａ-ｚＡ-Ｚ０-９]+)$/.test(str);

  }

  checkIsHiraganaChar(str) {

    return /^([ぁ-んａ-ｚＡ-Ｚ０-９]+)$/.test(str);

  }

  async checkExistFolder(directory, forlderName) {

    var result = false;
    var folder = await this.file.checkDir(directory, forlderName).catch(
      err => {
        result = false
      });
    if (folder) {
      result = true;
    }
    return result;

  }

  async getAllFile(url) {

    var currentFolder;
    currentFolder = await this.file.listDir(url, '').catch(
      error => {

      });
    if (currentFolder) {
      for (let subFolder of currentFolder) {
        if (subFolder.isFile) {
          this.listFile.push(subFolder);
        } else {
          await this.getAllFile(url + subFolder.name + '/')
        }
      }
    }

  }

  async getContentFile(contentsCode, stringSearch) {

    var url = null;
    var folderDirectory = this.file.dataDirectory + 'contents/' + contentsCode + '/';
    if (contentsCode) {
      this.listFileOfContent = [];
      await this.getAllFileForContent(folderDirectory);
      for (var i = 0; i < this.listFileOfContent.length; i++) {
        var filePath = this.listFileOfContent[i].fullPath;
        var fullFilePath = this.file.dataDirectory.slice(0, -1) + filePath;
        fullFilePath = fullFilePath.substr(7, fullFilePath.length);
        if (filePath.indexOf(stringSearch) != -1) {
          url = fullFilePath;
          break;
        }
      }
    }
    return url;

  }

  async getAllFileForContent(url) {

    var currentFolder;
    currentFolder = await this.file.listDir(url, '').catch(
      error => {

      });
    if (currentFolder) {
      for (let subFolder of currentFolder) {
        if (subFolder.isFile) {
          this.listFileOfContent.push(subFolder);
        } else {
          await this.getAllFileForContent(url + subFolder.name + '/')
        }
      }
    }

  }

  async downLoadContents(content) {

    var result = false;
    var data;
    var url;
    if (content.FileUrl == '') {
      console.log('download error: ' + content.FileUrl);
      return result;
    }
    var fileName = content.ContentCode + 'zip';
    var forderName = content.ContentCode;
    var folderExist = await this.checkExistFolder(this.file.dataDirectory, 'contents');
    if (folderExist) {
      var subFolderExist = await this.checkExistFolder(this.file.dataDirectory + 'contents/', forderName);
      if (subFolderExist) {
        await this.file.removeRecursively(this.file.dataDirectory + 'contents/', forderName).catch(err => {
          console.log(err);
        });
      }
      await this.file.createDir(this.file.dataDirectory + 'contents/', forderName, true).catch(err => {
        console.log('create dataDirectory:' + err);
      });
    } else {
      await this.file.createDir(this.file.dataDirectory, 'contents/', true).catch(err => {
        console.log('create dataDirectory:' + err);
      });;
      await this.file.createDir(this.file.dataDirectory + 'contents/', forderName, true).catch(err => {
        console.log('create dataDirectory: ' + err);
      });;
    }
    var downloadFolder = this.file.dataDirectory + 'contents/' + forderName;
    var downloadUrl = downloadFolder + '/' + fileName;
    url = this.encodeURINonASCII(content.FileUrl);
    data = await this.fileTransfer.download(url, downloadUrl).catch((error) => {
      console.log('download error: ' + content.FileUrl);
      result = false;
    });
    if (data) {
      console.log('download complete: ' + data.toURL());
      // Unzip file zip
      var unZipResult = await this.unzipFile(downloadUrl);
      if (unZipResult) {
        result = true;
      } else {
        result = false;
      }

    }
    return result;

  }
  async getContents() {

    var arrayContents = null;
    if (this.tokenObj != null) {
      arrayContents = await this.http.get(constant.API_CONTENTS_GET +
        "?SortId=1&RepresentativeFlg=" + this.tokenObj.userObject.RepresentativeFlg + "&StaffId=" + this.tokenObj.userObject.Id,
        this.tokenObj.options).toPromise()
        .catch(async error => {
          await this.showModalDialogConfirm(constant.NWK_CK_005, false);
          return arrayContents;
        });
      if (arrayContents) {
        arrayContents = arrayContents.json().items;
      }
    }
    return arrayContents;

  }
  async unzipFile(url) {

    var result = true;
    var folderPath = url.substr(0, url.lastIndexOf('/')) + '/';
    await this.zip.unzip(url, folderPath).catch((error) => {
      console.log('unzip error: ' + error);
      result = false;
    });
    return result;

  }

  /**
  * check first time 
  *    param: 
  *      -N/A
  *    return: 
  *      -isFirstTime
 */
  async checkIsFirstTime() {

    var isFirstTime = false;
    var data = await this.loadValueLocalStorage(constant.IS_FIRST_TIME).catch(
      error => {
        isFirstTime = true;
      });
    if (data != null) {
      if (data == true) {
        isFirstTime = false;
      }
    } else {
      isFirstTime = true;
    }
    return isFirstTime;

  }


}



