import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { TabsPage } from '../tabs/tabs';
import { Http } from '@angular/http';
import { Events } from 'ionic-angular';
import { PopoverController, Popover } from 'ionic-angular';
import { ContentsSortingOrderPopoverComponent } from './../../components/contents-sorting-order-popover/contents-sorting-order-popover';
import { FilterBySeriesPopoverComponent } from './../../components/filter-by-series-popover/filter-by-series-popover';
import { FilterByYamatoLanguageTagPopoverComponent } from './../../components/filter-by-yamato-language-tag-popover/filter-by-yamato-language-tag-popover';
import { AlertController } from 'ionic-angular';
import { commonClass } from './../../app/common';
import { constant } from './../../app/constant';
import { JwtHelper } from 'angular2-jwt';
import 'rxjs/add/operator/map';
import { File } from '@ionic-native/file';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { DatePipe } from '@angular/common';

/**
 * Generated class for the PlayListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
interface ContentPlay {
  Id: string,
  ContentName: string,
  ContentCode: string,
  CategoryId: string,
  CategoryName: string,
  seriesName: any,
  modified: Number,
  excutionCount: string,
  contentTag: any,
  LastPlayed: string,
  Level: string,
  thumbnailUrl: SafeResourceUrl,
  isUsed: boolean,
  jpDay: string,
  shortcontentTag: string
}

@Component({
  selector: 'page-play-list',
  templateUrl: 'play-list.html',

})
export class PlayListPage {

  showid: any;
  sortId: any;                                            // sort id
  items: any;                                             // array tag combobox
  series: any;
  arrayTag: Array<string>;                                 // array series combobox                        
  tabPage: any;                                           // tab page contructor
  labelTag1: string;                                      // label tag type show in combobox when user chose
  labelSeries: string;                                    // label series type show in combobox when user chose
  idTag1: any;                                            // id tag return
  idSeries: any;                                          // id series return 
  idSort: any;                                            //  id Sort return 
  labelIdSort: any;                                       // label sort type show in combobox when user chose 
  callEvent: Events;                                      // event 
  starText: string
  userObject: { options: any, userObject: any };
  searchText: any;
  public jwtHelper: JwtHelper = new JwtHelper();
  arraySearch: any;
  searchQuery: any
  imgList: Array<ContentPlay>;
  idBefore: any
  popover: Popover;
  public alertPresented: any;
  searchWord: string;
  directoryFolder: string = '';
  datePipe: any;
  listFolder: any;

  /* @constructor */
  constructor(public common: commonClass,
    public navCtrl: NavController,
    private http: Http,
    public navParams: NavParams,
    public popoverCtrl: PopoverController,
    public events: Events,
    public file: File,
    private sanitizer: DomSanitizer,
    private alertController: AlertController,
    public loadingCtrl: LoadingController) {

    this.directoryFolder = this.file.dataDirectory + 'contents/';
    this.imgList = [];
    this.tabPage = TabsPage;
    this.callEvent = events;
    // set default text
    this.labelIdSort = constant.DEFAULT_COMBOBOX_SORT;
    this.labelTag1 = constant.DEFAULT_COMBOBOX_TAG;
    this.labelSeries = constant.DEFAULT_COMBOBOX_TAG;

    this.arrayTag = [];
    this.alertPresented = false;
    this.datePipe = new DatePipe('jp');
    // get token
    this.userObject = this.common.tokenObj;

  }

  /**
  * load combobox
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async loadComboBox() {

    // load combobox tag 
    var tagData = await this.http.get(constant.API_TAGS_GET, this.userObject.options).toPromise()
      .catch(async error => {
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
        return;
      });
    if (tagData) {
      this.items = tagData.json().items;
    }
    // load combobox series
    var seriesData = await this.http.get(constant.API_SERIES_GET, this.userObject.options).toPromise()
      .catch(async error => {
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
        return;
      })
    if (seriesData) {
      this.series = seriesData.json().items;
    }

  }

  /**
  * Get contents from api
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async loadContent(TagId: any, Serries: any, idSort: any) {

    if (this.userObject != null) {
      // if Series variable = null => set label series = "サウンドパネル"
      if (this.idSeries == null) {
        Serries = "";
        this.labelSeries = constant.DEFAULT_COMBOBOX_TAG;
      }
      // if Tag variable = null => set label series = "かなでる"
      if (this.idTag1 == null) {
        TagId = "";
        this.labelTag1 = constant.DEFAULT_COMBOBOX_TAG;
      }
      //  if Tag variable = null => set label series = "新着順"
      if (this.idSort == null) {
        idSort = 1;
        this.labelIdSort = constant.DEFAULT_COMBOBOX_SORT;
      }
      // call api 
      var data;
      data = await this.http.get(constant.API_CONTENTS_GET +
        "?SortId=" + idSort +
        "&RepresentativeFlg=" + this.userObject.userObject.RepresentativeFlg +
        "&StaffId=" + this.userObject.userObject.Id +
        "&SeriesId=" + Serries +
        "&TagId=" + TagId,
        this.userObject.options).toPromise()
        .catch(async error => {
          await this.common.showModalDialogConfirm(constant.NWK_CK_005, false);
          return;
        });
      if (data) {
        data = data.json();
        this.imgList = [];
        this.arraySearch = [];
        var param = this.datePipe.transform(Date.now(), 'hhmmss');
        for (let i = 0; i < data.items.length; i++) {
          if (data.items[i].ContentCode.startsWith('NO')) {
            continue;
          }
          // Check content is already download and unzip or not
          var folderExist = await this.common.checkExistFolder(this.directoryFolder + '/' + data.items[i].ContentCode + '/', data.items[i].ContentCode);
          if (!folderExist) {
            continue;
          }
          // get thumbnail of content from folder download
          var thumbnailUrl = null;
          for (var y = 0; y < this.common.listFile.length; y++) {
            var filePath = this.common.listFile[y].fullPath;
            var filename = filePath.split('/').pop();
            var extension = filename.split('.').pop();
            if (filename == 'title_img.png') {
              if (filePath.indexOf(data.items[i].ContentCode + '/' + data.items[i].ContentCode + '/' + filename) != -1) {
                var fullFilePath = this.file.dataDirectory.slice(0, -1) + filePath;
                fullFilePath = fullFilePath.substr(7, fullFilePath.length);
                thumbnailUrl = fullFilePath + '?currentTime=' + param;
                break;
              }
            }
          }
          let content: ContentPlay = {
            Id: data.items[i].Id,
            ContentName: data.items[i].ContentName,
            ContentCode: data.items[i].ContentCode,
            CategoryId: data.items[i].CategoryId,
            CategoryName: data.items[i].CategoryName,
            seriesName: data.items[i].SeriesName,
            modified: data.items[i].Modified,
            excutionCount: data.items[i].ExecutionsCount,
            contentTag: data.items[i].contentstags,
            LastPlayed: data.items[i].LatestUse,
            Level: data.items[i].Level,
            thumbnailUrl: this.sanitizer.bypassSecurityTrustResourceUrl(thumbnailUrl),
            isUsed: true,
            jpDay: this.common.getJpDay(data.items[i].LatestUse) + 'に使用',
            shortcontentTag: ''
          }
          let strTag = '';
          content.contentTag.forEach(element => {
            strTag += element.Tag + ', ';
          });
          if (strTag.length > 1) strTag = strTag.substring(0, strTag.length - 2);
          if (strTag.length > 15) {
            content.shortcontentTag = strTag.substring(0, 15) + '・・・';
          }
          content.contentTag = strTag;
          if (data.items[i].LatestUse === constant.LAST_PLAYED_TEXT) {
            content.isUsed = false;
            content.LastPlayed = '未使用';
          }
          this.imgList.push(content);
          this.arraySearch.push(content)
        }
      }
    }

  }

  async ionViewDidLoad() {

    // write to behavior blog
    this.common.writeToBehaviorLog(constant.SCREEN_PLAYLIST_CODE, constant.MSG_BEHAVIOR_BLOG);
    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    loading.present();
    var check = await this.common.checkAccount();
    if (check) {
      // load content with tag = null, series = null, sort filter = 1 
      await this.loadContent('', '', 1);
      loading.dismiss();
      await this.loadComboBox();
    }

  }

  ionViewWillUnload() {

    this.popover && this.popover.dismiss();

  }

  /**
  * Open sorting combobox
  *    param: 
  *     ev:event
  *    return: 
  *      -N/A
 */
  async presentPopoverUserSortingOrder(ev) {

    // write to behavior blog
    this.common.writeToBehaviorLog(constant.SCREEN_SORT_CONTENT_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.popover = this.popoverCtrl.create(ContentsSortingOrderPopoverComponent, { itemSelect: this.idSort });
    this.popover.present({
      ev: ev
    });
    // when popup dimiss send id back
    this.popover.onDidDismiss(async data => {
      if (data == 1) {
        this.labelIdSort = constant.LABEL_SORT1;
        // if id == 1 set labelIdSort  = "新着順"
        this.idBefore = data;
      }
      else if (data == 2) {
        this.labelIdSort = constant.LABEL_SORT2;
        // if id == 2 set labelIdSort  = "人気順"
        this.idBefore = data;
      }
      else if (data == 3) {
        this.labelIdSort = constant.LABEL_SORT3;
        // if id == 3 set labelIdSort  = "レベル順"
        this.idBefore = data;
      }
      if (data == null) {
        data = this.idBefore;
        if (data == 1) {
          // if id == 1                    
          this.labelIdSort = constant.LABEL_SORT1;
          // set text combobox sort = "新着順"
          this.idBefore = data;
        }
        else if (data == 2) {
          // if id == 2                      
          this.labelIdSort = constant.LABEL_SORT2;
          // set text combobox sort = "人気順"
          this.idBefore = data;
        }
        else if (data == 3) {
          // if id == 3 
          this.labelIdSort = constant.LABEL_SORT3;
          // set text combobox sort = "人気順"
          this.idBefore = data;
        }
      }
      this.idSort = data;
      await this.loadContent(this.idTag1, this.idSeries, this.idSort);
    });

  }

  /**
  * Open filter by series combobox
  *    param: 
  *     ev:event
  *    return: 
  *      -N/A
 */
  async presentPopoverFilterBySeries(ev) {

    // write to behavior log
    this.common.writeToBehaviorLog(constant.SCREEN_SERIES_CODE, constant.MSG_BEHAVIOR_BLOG);
    // create pop up screen
    this.popover = this.popoverCtrl.create(FilterBySeriesPopoverComponent, { cbb: this.series, itemSelect: this.idSeries });
    this.popover.present({
      ev: ev
    });
    // action when pop up dismiss
    this.popover.onDidDismiss(async data => {
      if (data == "") {
        this.labelSeries = constant.DEFAULT_COMBOBOX_TAG;
        this.idSeries = '';
        await this.loadContent(this.idTag1, this.idSeries, this.idSort);
      }
      else {
        this.labelSeries = data.SeriesName;
        this.idSeries = data.Id;
        // load content base for user choice
        await this.loadContent(this.idTag1, this.idSeries, this.idSort);
      }
    });

  }
  /**
  * Open filter by language combobox
  *    param: 
  *     ev:event
  *    return: 
  *      -N/A
 */
  presentPopoverFilterByLanguage(ev) {

    // write to behavior log
    this.common.writeToBehaviorLog(constant.SCREEN_TAG_CODE, constant.MSG_BEHAVIOR_BLOG);
    // create pop up screen
    this.popover = this.popoverCtrl.create(FilterByYamatoLanguageTagPopoverComponent, { cba: this.items, itemSelect: this.idTag1 });
    this.popover.present({
      ev: ev
    });
    // action when pop up dismiss
    this.popover.onDidDismiss(async data => {
      if (data == "") {
        this.labelTag1 = constant.DEFAULT_COMBOBOX_TAG;
        this.idTag1 = '';
        await this.loadContent(this.idTag1, this.idSeries, this.idSort);
      }
      else {
        this.labelTag1 = data.Tag1;
        this.idTag1 = data.Id;
        // load content base for user choice
        await this.loadContent(this.idTag1, this.idSeries, this.idSort);
      }
    });

  }

  // pulish event for tab bar
  contentsCommentaryPage(img) {

    this.callEvent.publish(constant.EVENT_CONTENT_COMMENTARY, img, this.imgList);

  }
  /**
  * search function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  searchTap() {

    let val = this.searchWord;
    if (val && val.trim() != '') {
      this.arraySearch = this.imgList.filter((item) => {
        // return to lower case string
        return (item.ContentName.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.imgList;
    }
    if (this.arraySearch.length == 0) {
      this.present("", constant.ERROR_MESSAGE_SEARCHBAR)
    }

  }

  /**
  * Open sorting combobox
  *    param: 
  *     ev:event
  *    return: 
  *      -N/A
 */
  getItems(ev: any) {

    // get value in search textbox
    let val = ev.target.value;
    // delete white space and filter
    if (val && val.trim() != '') {
      this.arraySearch = this.imgList.filter((item) => {
        // return to lower case string
        return (item.ContentName.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.imgList;
    }
    if (this.arraySearch.length == 0) {
      this.present("", constant.ERROR_MESSAGE_SEARCHBAR);
    }

  }
  /**
  * Open alert view
  *    param: 
  *     title: string
  *     subTitle: string
  *    return: 
  *      -N/A
 */
  present(title, subTitle) {

    let vm = this
    if (!vm.alertPresented) {
      vm.alertPresented = true
      vm.alertController.create({
        title: title,
        subTitle: subTitle,
        buttons: [{
          text: 'OK',
          handler: () => {
            vm.alertPresented = false
          }
        }],
      }).present();
    }

  }

  /**
  * back to Login
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  returnLogin() {

    // puslish event back to login
    this.callEvent.publish(constant.EVENT_CHANGE_LOGIN_PAGE);

  }
  
  /**
  * show content Tag
  *    param: 
  *      -id: any
  *    return: 
  *      -N/A
 */
  showcontentTag(id) {
    this.showid = id;
  }

}





