import { Component, ViewChild } from '@angular/core';
import { Navbar, ViewController, Events } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { commonClass } from './../../app/common';
import { constant } from './../../app/constant';

/**
 * Generated class for the SettingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-setting',
  templateUrl: 'setting.html',
})

export class SettingPage {
  @ViewChild(Navbar) navBar: Navbar;
  callEvent: Events;

  constructor(public viewCtr: ViewController,
    private iab: InAppBrowser,
    public events: Events,
    public common: commonClass) {

    this.callEvent = events;

  }

  /**
  * open staff website
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  openStaff() {

    this.iab.create(constant.STAFF_LINK);

  }

  /**
  * load Facilities website
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  openFacilities() {

    this.iab.create(constant.FACILITY_LINK);

  }

  async ionViewDidLoad() {
    
    await this.common.writeToBehaviorLog(constant.SCREEN_SETTING_CODE, constant.MSG_BEHAVIOR_BLOG);
    // handle event back button to chat mode screen
    this.navBar.backButtonClick = (e: UIEvent) => {
      this.viewCtr.dismiss();
      this.callEvent.publish('chatPage');
    };

  }

}



