import { Component } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular/index';

/**
 * Generated class for the UserSortingOrderPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'user-sorting-order-popover',
  templateUrl: 'user-sorting-order-popover.html'
})
export class UserSortingOrderPopoverComponent {

  idText: any;
  text: string;
  itemselect: any;
  item: any;
  arrayOfText: [string];

  constructor(private viewCtrl: ViewController, public nav: NavParams) {

    this.itemselect = this.nav.get("itemSelect");
    if (this.itemselect == 1) {
      this.itemselect = "五十音順"
    }
    else if (this.itemselect == 2) {
      this.itemselect = "スコア順"
    }
    else if (this.itemselect == 3) {
      this.itemselect = "プレイ日順"
    }
    this.arrayOfText = ["五十音順", "スコア順", "プレイ日順"];

  }

  /**
  * go to detail screen 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  goToDetails() {

    if (this.itemselect == "五十音順") {
      this.idText = 1;
    }
    else if (this.itemselect == "スコア順") {
      this.idText = 2;
    }
    else {
      this.idText = 3;
    }
    this.viewCtrl.dismiss(this.idText);

  }

  /**
  * action cancel button 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss(this.idText);

  }

}



