import { Component } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular/index';

/**
 * Generated class for the ContentsSortingOrderPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'contents-sorting-order-popover',
  templateUrl: 'contents-sorting-order-popover.html'
})

export class ContentsSortingOrderPopoverComponent {

  arrayOfText: [string];
  text: string;
  idText: any;
  itemselect: any;
  item: any;

  constructor(private viewCtrl: ViewController,
    public nav: NavParams) {

    this.itemselect = this.nav.get("itemSelect");
    if (this.itemselect == 1) {
      this.itemselect = "新着順"
    }
    else if (this.itemselect == 2) {
      this.itemselect = "人気順"
    }
    else if (this.itemselect == 3) {
      this.itemselect = "レベル順"
    }
    else {
      this.itemselect = ""
    }
    this.arrayOfText = ["新着順", "人気順", "レベル順"];

  }

  /**
  * go to details screen
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  goToDetails() {

    if (this.itemselect == "新着順") {
      this.idText = 1;
    }
    else if (this.itemselect == "人気順") {
      this.idText = 2;
    }
    else {
      this.idText = 3;
    }
    this.viewCtrl.dismiss(this.idText);

  }

  /**
  * cancel button action
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss();

  }

}




