import { Component } from '@angular/core';
import { NavController, LoadingController, NavParams, ViewController } from 'ionic-angular';
import { AuthRequestor } from './auth_requestor';
import { LoginInputValidator } from './login_input_validator';
import { LoginUser } from './login_user';
import { TabsPage } from './../tabs/tabs';
import 'rxjs/add/operator/map';
import { constant } from './../../app/constant';
import { commonClass } from './../../app/common';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Events } from 'ionic-angular';
import { WalkThroughPage } from './../walkthrough/walkthrough';
import { ModalController } from 'ionic-angular';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';



@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {

  isFirstTime: boolean = false;
  public isPasswordShown = false;
  public errorType: string = "";
  loading: any;
  callEvent: Events;
  fileTransfer: FileTransferObject = this.transfer.create();
  userObject: { options: any, userObject: any };
  loginUser: LoginUser =
    {
      email: "",
      password: "",
    };

  constructor(public navCtrl: NavController,
    public authRequestor: AuthRequestor,
    public common: commonClass,
    private iab: InAppBrowser,
    public events: Events,
    public navParams: NavParams,
    public modalCtrl: ModalController,
    private transfer: FileTransfer,
    private file: File,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController) {

    this.callEvent = events

  }

  /**
   * If password input type is password, then display password value as plain text. If not, display it as password text
   */
  onShowPassword(): void {

    if (this.loginUser.password !== "") {
      this.isPasswordShown = !this.isPasswordShown;
    }

  }

  /**
   * Perform login job
   */
  async onSubmitLogin() {

    this.loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    this.loading.present()
    this.errorType = "";
    const inputValidator: LoginInputValidator = new LoginInputValidator();
    if (this.loginUser.email == "" && this.loginUser.password == "") {
      this.errorType = constant.NWK_CK_001.replace('{0}', constant.EMAIL_ADDRESS + '、' + constant.PASSWORD);
      this.loading.dismiss();
      this.showInvalidInputErrorMessage();
    } else {
      if (this.loginUser.email == "") {
        this.errorType = constant.NWK_CK_001.replace('{0}', constant.EMAIL_ADDRESS);
        this.loading.dismiss()
        this.showInvalidInputErrorMessage();
      } else {
        if (this.loginUser.password == "") {
          this.errorType = constant.NWK_CK_001.replace('{0}', constant.PASSWORD);
          this.loading.dismiss()
          this.showInvalidInputErrorMessage();
        } else {
          if (!inputValidator.isEmailValid(this.loginUser.email)) {
            this.errorType = constant.NWK_CK_002.replace('{0}', constant.EMAIL_ADDRESS);
            this.loading.dismiss()
            this.showInvalidInputErrorMessage();
          } else if (!inputValidator.isPasswordValid(this.loginUser.password)) {
            this.errorType = constant.NWK_CK_002.replace('{0}', constant.PASSWORD);
            this.loading.dismiss()
            this.showInvalidInputErrorMessage();
          } else {
            this.hideInvalidInputErrorMessage();
            this.requestAuthToServer();
          }
        }
      }
    }
  }

  /**
   * Show invalid input error message
   */
  showInvalidInputErrorMessage(): void {

    const errMsgSpan = document.getElementById(constant.ERROR_MEASAGE_ID);
    errMsgSpan.hidden = false;

  }

  /**
  * Hide invalid input error message
  */
  hideInvalidInputErrorMessage(): void {

    const errMsgSpan = document.getElementById(constant.ERROR_MEASAGE_ID);
    errMsgSpan.hidden = true;

  }

  /**
   * request authentication to Server
   */
  requestAuthToServer() {

    const loginData = (this.loginUser);
    let loginObserver = this.authRequestor.requestAuth(loginData);
    loginObserver.subscribe(async data => {
      var result = await this.common.saveValueLocalStorage(constant.TOKEN_TEXT, data);
      if (result) {
        this.loading.dismiss();
        this.common.tokenObj = await this.common.getToken();
        var isFirstTime = await this.common.checkIsFirstTime();
        if (isFirstTime) {
          await this.proccessDownloadContents();
        } else {
          this.navCtrl.push(TabsPage);
        }
      }
    }, error => {
      this.loading.dismiss();
      let mes = JSON.parse(error._body);
      this.errorType = mes.result;
      this.showInvalidInputErrorMessage();
    }
    );

  }

  /**
  * Open forget password link
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  openForgetPassword() {

    this.iab.create(constant.FORGET_PASSWORD_LINK);

  }

  /**
  * download content
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async proccessDownloadContents() {

    var isShowDialog = false;
    var resultDownload = false;
    var countFileError = 0;
    var confirm;
    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.MEASAGE_WAIT
    });;
    var contentsList = await this.common.getContents();
    if (contentsList == null) {
      return;
    }
    var storageDownloadList = await this.common.loadValueLocalStorage(constant.DOWNLOAD_CONTENT_HISTORY);
    if (storageDownloadList == null) {
      storageDownloadList = [];
      confirm = await this.common.showModalDialogAsk(constant.NWK_CK_063);
      isShowDialog = true;
      if (confirm != true) {
        this.navCtrl.push(TabsPage);
        loading.dismiss();
      }
      else {
        loading.present();
        for (var i = 0; i < contentsList.length; i++) {
          resultDownload = await this.common.downLoadContents(contentsList[i]);
          if (resultDownload) {
            storageDownloadList.push({ contentId: contentsList[i].Id, updateDate: contentsList[i].Modified, countUpdate: 0 });
          } else {
            countFileError = countFileError + 1;
          }
        }

        await this.common.saveValueLocalStorage(constant.DOWNLOAD_CONTENT_HISTORY, storageDownloadList);
        if (isShowDialog == true) {
          // Get all content file.
          this.common.listFile = [];
          var directoryFolder = this.file.dataDirectory + 'contents/';
          await this.common.getAllFile(directoryFolder);
          loading.dismiss();
          if (countFileError > 0) {
            await this.common.showModalDialogConfirm(constant.NWK_CK_004, false);
            this.navCtrl.push(TabsPage);
          } else {
            await this.common.showModalDialogConfirm(constant.NWK_CK_064, false)
            let walkthroughPage = this.modalCtrl.create(WalkThroughPage, { isFirstTimeStart: true }, { enableBackdropDismiss: false });
            walkthroughPage.present();
          }
        }
      }
    }

  }

  async ionViewWillEnter() {

    this.callEvent.subscribe(constant.CHANGE_TAB_PAGE, () => {
      this.navCtrl.push(TabsPage)
    });

  }

  ionViewWillLeave() {

    this.events.unsubscribe(constant.CHANGE_TAB_PAGE);

  }
}




