import { WalkThroughPage } from './../walkthrough/walkthrough';
import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { PopoverController, Popover } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { SpecifyPeriodPopoverComponent } from './../../components/specify-period-popover/specify-period-popover';
import { Http } from '@angular/http';
import { JwtHelper } from 'angular2-jwt';
import { commonClass } from './../../app/common';
import { constant } from './../../app/constant';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { AlertController } from 'ionic-angular';
import { Zip } from '@ionic-native/zip';

/**
 * Generated class for the ChatModePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-chat-mode',
  templateUrl: 'chat-mode.html',
})

export class ChatModePage {
  // Declare variable
  callEvent: Events;
  public jwtHelper: JwtHelper = new JwtHelper();
  arraySearch: any;
  noticesList: any;
  initDate: Date = new Date();                                                        // Current date time of system
  endDate: Date;                                                                      // End date for search notices    
  startDate: Date;
  public alertPresented: any;                                                               // Start date for search notices
  isFirstTime: boolean = false;                                                       // If First time of start app flag      
  messageThanks: String = constant.MSG_THANKS_INSTALL;                                // The Content of message thanks for install
  userObject: { options: any, userObject: any };                                      // The token of user.
  fileTransfer: FileTransferObject = this.transfer.create();
  searchValue: string = '';
  remainDaysNotices: any;
  popover: Popover;
  searchWord: string;
  loading: any;

  /**
   * load all Notices
   *    param: 
   *      -events
   *      -http
   *      -navCtrl
   *      -nativeStorage
   *      -popoverCtrl
   *      -navParams
   *      -modalCtrl
   *      -common
   *    return: 
   *      -N/A
  */
  constructor(public events: Events,
    private http: Http,
    public navCtrl: NavController,
    private popoverCtrl: PopoverController,
    public navParams: NavParams,
    public modalCtrl: ModalController,
    public common: commonClass,
    private transfer: FileTransfer,
    private file: File,
    public loadingCtrl: LoadingController,
    private zip: Zip, private alertController: AlertController) {

    // Set default value for token object
    this.userObject = { options: null, userObject: null };
    this.callEvent = events;
    this.startDate = common.startDate;
    this.endDate = common.endDate;
    events.subscribe(constant.EVENT_LOAD_NOTICES, (newStartDate, newEndDate) => {
      this.endDate = newEndDate;
      this.startDate = newStartDate;
      common.startDate = newStartDate;
      common.endDate = newEndDate;
      this.loadAllNotices(newStartDate, newEndDate);
    });

  }

  /**
   * load all Notices
   *    param: 
   *      -newStartDate:  start date for get all message
   *      -newEndDate:  end date for get all message
   *    return: 
   *      -N/A
  */
  async loadAllNotices(startDate, endDate) {

    this.searchValue = "";    
    if (this.userObject != null) {
      // Get Notice from api
      this.noticesList = await this.http.get(constant.API_NOTICES_GET +
        "?starttime=" + this.common.formatDate(this.startDate) +
        "&endtime=" + this.common.formatDate(this.endDate) +
        "&RepresentativeFlg=" + this.userObject.userObject.RepresentativeFlg, this.userObject.options).toPromise()
        .catch(async error => {
          this.loading.dismiss();
          await this.common.showModalDialogConfirm(constant.NWK_CK_005, false);
          return;
        });
      if (this.noticesList) {
        this.noticesList = this.noticesList.json().items;
        // Sort array
        this.noticesList = this.noticesList.sort(function (a, b) {
          return Date.parse(a.Modified) - Date.parse(b.Modified)
        });
      }
      if (this.isFirstTime) {
        if (this.common.formatDate(this.initDate) >= this.common.formatDate(this.startDate)
          && this.common.formatDate(this.initDate) <= this.common.formatDate(this.endDate)) {
          this.noticesList.push({ Created: new Date(), Modified: new Date(), Notice: constant.MSG_THANKS_INSTALL });
        }
      }
      if (this.common.accountRemainingDays != null) {
        if (this.common.formatDate(this.initDate) >= this.common.formatDate(this.startDate)
          && this.common.formatDate(this.initDate) <= this.common.formatDate(this.endDate)) {
          this.noticesList.push({ Created: new Date(), Modified: new Date(), Notice: constant.MSG_REGISTRATION_REQUEST.replace('（残日数）', String(this.common.accountRemainingDays)) });
        }
      }
      this.arraySearch = this.noticesList;
      if (this.arraySearch.length == 0) {
        this.loading.dismiss();
        this.common.showModalDialogConfirm(constant.NWK_CK_003, false);
      }
    }

  }

  /**
   * Page load event
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  async ionViewDidLoad() {
    
    this.userObject = this.common.tokenObj;
    this.common.writeToBehaviorLog(constant.SCREEN_CHATMODE_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    this.loading.present();
    var check = await this.common.checkAccount();
    if (check) {
      this.isFirstTime = await this.common.checkIsFirstTime();
      if(this.isFirstTime){
        this.common.saveValueLocalStorage(constant.IS_FIRST_TIME, true);
      }
      await this.loadAllNotices(this.startDate, this.endDate);
      await this.proccessDownloadContents();
    }
    else {
      this.loading.dismiss();
    }

  }

  /**
   * Get contents from api
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  async getContents() {

    var arrayContents = null;
    if (this.userObject != null) {
      arrayContents = await this.http.get(constant.API_CONTENTS_GET +
        "?SortId=1&RepresentativeFlg=" + this.userObject.userObject.RepresentativeFlg + "&StaffId=" + this.userObject.userObject.Id,
        this.userObject.options).toPromise()
        .catch(async error => {
          this.loading.dismiss();
          await this.common.showModalDialogConfirm(constant.NWK_CK_005, false);
          return arrayContents;
        });
      if (arrayContents) {
        arrayContents = arrayContents.json().items;
      }
    }
    return arrayContents;

  }

  /**
  * Proccess of downloading file zip
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async proccessDownloadContents() {

    var isShowDialog = false;
    var resultDownload = false;
    var countFileError = 0;
    var confirm;
    let downloading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.MEASAGE_WAIT
    });
    var contentsList = await this.getContents();
    this.loading.dismiss();
    if (contentsList == null) {
      return;
    }
    var storageDownloadList = await this.common.loadValueLocalStorage(constant.DOWNLOAD_CONTENT_HISTORY);
    if (storageDownloadList == null) {
      storageDownloadList = [];
      confirm = await this.common.showModalDialogAsk(constant.NWK_CK_063);
      isShowDialog = true;
      if (confirm != true) {
        return;
      }
      downloading.present();
      for (var i = 0; i < contentsList.length; i++) {
        resultDownload = await this.downLoadContents(contentsList[i]);
        if (resultDownload) {
          storageDownloadList.push({ contentId: contentsList[i].Id, updateDate: contentsList[i].Modified, countUpdate: 0 });
        } else {
          countFileError = countFileError + 1;
        }
      }

    }
    else {
      for (var y = 0; y < contentsList.length; y++) {
        var index = this.common.findWithAttr(storageDownloadList, constant.CONTENT_ID_TEXT, contentsList[y].Id);
        if (index == -1) {
          if (isShowDialog == false) {
            confirm = await this.common.showModalDialogAsk(constant.NWK_CK_063);
            if (confirm != true) {
              return;
            } else {
              isShowDialog = true;
              downloading.present();
            }
          }
          resultDownload = await this.downLoadContents(contentsList[y]);
          if (resultDownload) {
            storageDownloadList.push({ contentId: contentsList[y].Id, updateDate: contentsList[y].Modified, countUpdate: 0 });
          } else {
            countFileError = countFileError + 1;
          }
        }
        else {
          if (contentsList[y].Modified != storageDownloadList[index].updateDate) {
            if (isShowDialog == false) {
              confirm = await this.common.showModalDialogAsk(constant.NWK_CK_063);
              if (confirm != true) {
                return;
              } else {
                isShowDialog = true;
                downloading.present();
              }
            }
            resultDownload = await this.downLoadContents(contentsList[y]);
            if (resultDownload) {
              storageDownloadList[index].updateDate = contentsList[y].Modified;
              storageDownloadList[index].countUpdate = storageDownloadList[index].countUpdate + 1;
            } else {
              countFileError = countFileError + 1;
            }
          }
        }
      }
    }
    await this.common.saveValueLocalStorage(constant.DOWNLOAD_CONTENT_HISTORY, storageDownloadList);
    if (isShowDialog == true) {
      // Get all content file.
      this.common.listFile = [];
      var directoryFolder = this.file.dataDirectory + 'contents/';
      await this.common.getAllFile(directoryFolder);
      downloading.dismiss();
      if (countFileError > 0) {
        this.common.showModalDialogConfirm(constant.NWK_CK_004, true);
      } else {
        this.common.showModalDialogConfirm(constant.NWK_CK_064, true);
      }
    }

  }

  searchTap() {

    let val = this.searchWord;
    if (val && val.trim() != '') {
      this.arraySearch = this.noticesList.filter((item) => {
        return (item.Notice.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.noticesList;
    }
    if (this.arraySearch.length == 0) {
      this.present("", constant.NWK_CK_003)
    }

  }

  /**
   * Search Notice by keyword
   *    param: 
   *      -ev: event.
   *    return: 
   *      -N/A
  */
  searchItem(ev: any) {

    let val = ev._value;
    if (val && val.trim() != '') {
      this.arraySearch = this.noticesList.filter((item) => {
        return (item.Notice.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.noticesList;
    }
    if (this.arraySearch.length == 0) {
      this.present("", constant.NWK_CK_003)
    }
  }

  present(title, subTitle) {
    // only one alert call when tap japanese - add by Dat - need to rewrite in common class
    let vm = this
    if (!vm.alertPresented) {
      vm.alertPresented = true
      vm.alertController.create({
        title: title,
        subTitle: subTitle,
        buttons: [{
          text: constant.BUTTON_OK,
          handler: () => {
            vm.alertPresented = false
          }
        }],
      }).present();
    }

  }

  /**
   * Show introduce screen
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  showWalkthroughModal() {

    let walkthroughPage = this.modalCtrl.create(WalkThroughPage, { isFirstTimeStart: false });
    walkthroughPage.present();

  }

  /**
   * Show calendar
   *    param: 
   *      -ev: event
   *    return: 
   *      -N/A
  */
  showSpecifyingPeriod(ev) {

    this.common.writeToBehaviorLog(constant.SCREEN_SPECIFYING_PERIOD_CODE, constant.MSG_BEHAVIOR_BLOG)
    this.popover = this.popoverCtrl.create(SpecifyPeriodPopoverComponent, { startDate: this.startDate, endDate: this.endDate, eventName: constant.EVENT_LOAD_NOTICES });
    this.popover.present({ ev: ev });

  }

  /**
   * Page leave event
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  ionViewWillLeave() {

    this.events.unsubscribe(constant.EVENT_LOAD_NOTICES);

  }

  /**
   * Download file zip
   *    param: 
   *      -fileUrl: link of download file
   *    return: 
   *      -N/A
  */
  async downLoadContents(content) {

    var result = false;
    var data;
    var url;
    if (content.FileUrl == '') {
      return result;
    }
    var fileName = content.ContentCode + constant.ZIP_EXTENSION;
    var forderName = content.ContentCode;
    var folderExist = await this.common.checkExistFolder(this.file.dataDirectory, 'contents');
    if (folderExist) {
      var subFolderExist = await this.common.checkExistFolder(this.file.dataDirectory + 'contents/', forderName);
      if (subFolderExist) {
        await this.file.removeRecursively(this.file.dataDirectory + constant.CONTENT_TEXT, forderName).catch(err => {

        });
      }
      await this.file.createDir(this.file.dataDirectory + constant.CONTENT_TEXT, forderName, true).catch(err => {

      });
    } else {
      await this.file.createDir(this.file.dataDirectory, constant.CONTENT_TEXT, true).catch(err => {

      });;
      await this.file.createDir(this.file.dataDirectory + constant.CONTENT_TEXT, forderName, true).catch(err => {

      });;
    }
    var downloadFolder = this.file.dataDirectory + constant.CONTENT_TEXT + forderName;
    var downloadUrl = downloadFolder + '/' + fileName;
    url = this.common.encodeURINonASCII(content.FileUrl);
    data = await this.fileTransfer.download(url, downloadUrl).catch((error) => {

      result = false;
    });
    if (data) {
      // Unzip file zip
      var unZipResult = await this.unzipFile(downloadUrl);
      if (unZipResult) {
        result = true;
      } else {
        result = false;
      }

    }
    return result;

  }
  
  /**
  * unzip file
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async unzipFile(url) {

    var result = true;
    var folderPath = url.substr(0, url.lastIndexOf('/')) + '/';
    await this.zip.unzip(url, folderPath).catch((error) => {

      result = false;
    });
    return result;

  }

  /**
  * log out function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  logout() {

    this.events.publish(constant.EVENT_CHANGE_LOGIN_PAGE);

  }

}



