import { Component } from '@angular/core';
import { Events, IonicPage, NavController, NavParams, AlertController, ModalController, LoadingController } from 'ionic-angular';
import { SpecifyPeriodPopoverComponent } from './../../components/specify-period-popover/specify-period-popover';
import { Http } from '@angular/http';
import { JwtHelper } from 'angular2-jwt';
import { PopoverController, Popover } from 'ionic-angular';
import { UpdatingUserComponent } from './../../components/Updating-User/Updating-User';
import { constant } from './../../app/constant';
import { commonClass } from './../../app/common';
import { ContentsCommentaryPage } from '../contents-commentary/contents-commentary';
import { DatePipe } from '@angular/common';
import { DetailPopUpComponent } from './../../components/detail-pop-up/detail-pop-up';
import { File } from '@ionic-native/file';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

/**
 * Generated class for the UserDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-detail',
  templateUrl: 'user-detail.html',
  providers: [DatePipe]
})

export class UserDetailPage {
  userObject: { options: any, userObject: any };
  public jwtHelper: JwtHelper = new JwtHelper();
  public idItem;
  objectFacilities: any;
  token: string;
  arrayFacilities: any;
  arrayStaff: any;
  arrayHitories: any;
  arrayNOAHHistories: any;
  arrayMerge: any[];
  arraySearch: any;
  endDate: Date = new Date();
  startDate: Date = new Date(new Date().setDate(this.endDate.getDate() - 30));
  UserInfo: any;
  changeId: string;
  eventname: String = "loadAllNotices";
  arrayOfText: any
  level: any
  labelText: string
  searchWord: string;
  popover: Popover;

  constructor(public navCtrl: NavController,
    private datePipe: DatePipe,
    public file: File,
    public navParams: NavParams,
    private events: Events,
    public modalCtrl: ModalController,
    private alertController: AlertController,
    private http: Http,
    private popoverCtrl: PopoverController,
    private sanitizer: DomSanitizer,
    public common: commonClass,
    public loadingCtrl: LoadingController) {

    this.userObject = this.common.tokenObj;
    this.arrayOfText = [1, 2, 3];
    this.labelText = constant.LABEL_TEXT
    this.idItem = navParams.get("idItem");
    this.events.subscribe(String(this.eventname), (newStartDate, newEndDate) => {
      this.endDate = newEndDate;
      this.startDate = newStartDate;
      this.updateDetail();
    });

  }

  /**
  * update user detail
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  async updateDetail() {

    await this.getHistories();
    await this.getNOAHHistories();
    await this.getThumbnailImage();
    await this.mergeHistoriesNOAHHistories();
    this.arraySearch = this.arrayHitories;

  }

  /**
  * open pdf report screen
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  openFilters() {

    this.common.writeToBehaviorLog(constant.SCREEN_PDF_REPORT_CODE, constant.MSG_BEHAVIOR_BLOG);
    let alert = this.alertController.create({
      subTitle: constant.MSG_PDF_REPORT,
      buttons: [constant.BUTTON_OK]
    });
    alert.present();

  }

  async ionViewDidLoad() {

    this.common.writeToBehaviorLog(constant.SCREEN_USER_DETAIL_CODE, constant.MSG_BEHAVIOR_BLOG);
    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    loading.present();
    await this.getUserInfo(this.idItem);
    if (this.UserInfo) {
      await this.getFacilities(this.UserInfo.AgreementId);
      if (this.arrayFacilities) {
        let item = this.arrayFacilities.filter(a => a.Id == this.UserInfo.FacilityId);
        this.UserInfo.Facility = item[0];
      } else this.UserInfo.Facility = {};

      await this.getStaff(this.UserInfo.AgreementId);
      if (this.arrayStaff) {
        let item = this.arrayStaff.filter(a => a.Id == this.UserInfo.StaffId);
        this.UserInfo.Staff = item[0];
      } else this.UserInfo.Staff = {};

      this.UserInfo.Age = this.common.calculateAge(new Date(this.UserInfo.Birthday));
      this.UserInfo.BirthdayJP = this.common.convertToJpDate(this.UserInfo.Birthday);
    }

    await this.getHistories();
    await this.getNOAHHistories();
    await this.getThumbnailImage();
    this.mergeHistoriesNOAHHistories();
    this.arraySearch = this.arrayHitories;
    loading.dismiss();
    
  }

  ionViewWillUnload() {

    this.popover && this.popover.dismiss();

  }

  /**
  * show specifying period
  *    param: 
  *      -ev:event
  *    return: 
  *      -N/A
  */

  showSpecifyingPeriod(ev) {

    this.common.writeToBehaviorLog(constant.SCREEN_SPECIFYING_DETAIL_CODE, constant.MSG_BEHAVIOR_BLOG)
    this.popover = this.popoverCtrl.create(SpecifyPeriodPopoverComponent, { startDate: this.startDate, endDate: this.endDate, eventName: this.eventname });
    this.popover.present({ ev: ev });

  }

  /**
  * search function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  searchTap() {

    let val = this.searchWord;
    // delete white space and filter
    if (val && val.trim() != '') {
      console.log(1);
      // return to lower case string
      this.arrayHitories = this.arraySearch.filter((item) => {
        if (item.ContentName.toLowerCase().indexOf(val.toLowerCase()) > -1
          || item.CategoryName.toLowerCase().indexOf(val.toLowerCase()) > -1)
          return item;
      })
    } else {
      console.log(2);
      this.arrayHitories = this.arraySearch;
    }
    this.mergeHistoriesNOAHHistories();

  }

  /**
  * search function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async getItems(ev: any) {

    // get value in search textbox
    let val = ev.target.value;
    // delete white space and filter
    if (val && val.trim() != '') {
      // return to lower case string
      this.arrayHitories = this.arraySearch.filter((item) => {
        if (item.ContentName.toLowerCase().indexOf(val.toLowerCase()) > -1
          || item.CategoryName.toLowerCase().indexOf(val.toLowerCase()) > -1)
          return item;
      })
    } else {
      this.arrayHitories = this.arraySearch;
    }
    this.mergeHistoriesNOAHHistories();

  }

  /**
  * merge history with noAH History array
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  mergeHistoriesNOAHHistories() {

    this.arrayMerge = [];
    let arrtemp = [];

    if (this.arrayHitories) {
      this.arrayHitories.forEach(element => {
        element.is = 'his';
        arrtemp.push(element);
      });
    }
    if (this.arrayNOAHHistories) {
      this.arrayNOAHHistories.forEach(element => {
        element.is = 'noah';
        arrtemp.push(element);
      });
    }
    this.datePipe
    arrtemp.forEach(e => {
      let date = new Date(e.Modified);
      e.Date = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
      e.Date = this.datePipe.transform(e.Date, constant.FORMAT_DATE);
    });
    let arrtemp2 = this.unique(arrtemp);
    arrtemp2.forEach(e => {
      let item = {
        'date': this.common.formatDate2Jp(e, false),
        'historiesdata': [],
        'NOAHdata': []
      };
      let arrtemp3 = arrtemp.filter(a => a.Date == e);
      arrtemp3.forEach(element => {
        if (element.is == 'his') {
          if (this.UserInfo.ChallengeScore == -1 && this.UserInfo.TraningScore == -1) {
            element.Score = "-"
          }
          item.historiesdata.push(element);
        } else item.NOAHdata.push(element);
      });
      this.arrayMerge.push(item);
    });

  }

  /**
  * get facilities combobox
  *    param: 
  *      -AgreementId: string
  *    return: 
  *      -N/A
  */
  async getFacilities(AgreementId) {

    var data = await this.http.get(constant.API_FACILITIES_GET + "?AgreementId=" + AgreementId, this.userObject.options)
      .toPromise().catch(
      async err => {
        // if error show meseage       
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
      });
    if (data) {
      this.arrayFacilities = data.json().items;
    }

  }

  /**
  * get staff combobox
  *    param: 
  *      -AgreementId: string
  *    return: 
  *      -N/A
  */

  async getStaff(AgreementId) {

    var data = await this.http.get(constant.API_STAFFS_GET + "?AgreementId=" + AgreementId, this.userObject.options)
      .toPromise().catch(
      async err => {
        // if error show meseage       
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
      });
    if (data) {
      this.arrayStaff = data.json().items;
    }

  }

  /**
  * get history 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  async getHistories() {

    var data = await this.http.get(constant.API_HISTORIES + "?userId=" + this.idItem + "&startDate=" + this.common.formatDate(this.startDate) + "&endDate=" + this.common.formatDate(this.endDate), this.userObject.options)
      .toPromise().catch(async err => {
        // if error show meseage       
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
      });
    if (data) {
      this.arrayHitories = data.json().items;
    }

  }

  /**
  * get NoAH history 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  async getNOAHHistories() {

    var data = await this.http.get(constant.API_NOAHHISTORIES + "?userId=" + this.idItem + "&startDate=" + this.common.formatDate(this.startDate) + "&endDate=" + this.common.formatDate(this.endDate), this.userObject.options)
      .toPromise().catch(async err => {
        // if error show meseage       
        await this.common.showModalDialogConfirm(constant.NWK_CK_065, false);
      });
    if (data) {
      this.arrayNOAHHistories = data.json().items;
    }

  }

  /**
  * get User info 
  *    param: 
  *      -id: string
  *    return: 
  *      -N/A
  */

  async getUserInfo(id: string) {

    let AgreementId = this.userObject.userObject.AgreementId;
    var data = await this.http.get(constant.API_GET_USER + "?userId=" + this.idItem + "&AgreementId=" + AgreementId, this.userObject.options)
      .toPromise().catch(err => {
        console.log(err)
      });
    if (data) {
      this.UserInfo = data.json().items[0];
    }
  }

  async idChange() {

    switch (this.changeId) {
      case 'id1':
        await this.getHistories();
        await this.getNOAHHistories();
        await this.getThumbnailImage();
        break;
      case 'id2':
        await this.getHistories();
        this.arrayNOAHHistories = null;
        await this.getThumbnailImage();
        break;
      case 'id3':
        this.arrayHitories = null;
        await this.getNOAHHistories();
        break;
    }
    this.mergeHistoriesNOAHHistories();

  }

  openUpdatingUserForm() {

    this.common.writeToBehaviorLog(constant.SCREEN_UPDATE_USER_CODE, constant.MSG_BEHAVIOR_BLOG);
    let modal = this.modalCtrl.create(UpdatingUserComponent, { user: this.UserInfo }, { enableBackdropDismiss: false });
    modal.present();
    modal.onDidDismiss(data => {
      if (data == '1') {
        this.navCtrl.pop();
      }
    });

  }

  /**
  * delete user
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  deleteUser() {

    this.common.writeToBehaviorLog(constant.SCREEN_DELETE_USER_DETAIL_CODE, constant.MSG_BEHAVIOR_BLOG);
    let alert = this.alertController.create({
      subTitle: constant.NWK_CK_067,
      buttons: [
        constant.MSSG_TEXT,
        {
          text: constant.CLOSE_TEXT,
          handler: () => {
            this.confirmDeletingUser();
          }
        }
      ]
    });
    alert.present();

  }

  /**
  * confrim delete user
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  async confirmDeletingUser() {

    this.userObject.options.body = this.idItem;
    this.http.delete(constant.API_DELETE_USER, this.userObject.options)
      .subscribe((response) => {
        this.navCtrl.pop();
        this.events.publish(constant.BACK_TO_USER_LIST);
      },
      (error) => {
        let alert = this.alertController.create({
          subTitle: constant.NWK_CK_066,
          buttons: [constant.BUTTON_OK]
        });
        alert.present();
      });

  }

  /**
  * go to content commentary 
  *    param: 
  *      -item: object
  *    return: 
  *      -N/A
  */
  goTocontentsCommentaryPage(item) {

    this.navCtrl.push(ContentsCommentaryPage, {
      idItem: item
    });

  }

  /**
  * check unique item
  *    param: 
  *      -item: object
  *    return: 
  *      -N/A
  */

  unique(items) {

    let arr = [];
    for (let i = 0; i < items.length; i++) {
      if (arr.indexOf(items[i].Date) < 0) {
        arr.push(items[i].Date);
      }
    }
    return arr;

  }

  /**
  * show level pop up
  *    param: 
  *      -event: event
  *    return: 
  *      -N/A
  */

  levelPopover(event) {

    this.common.writeToBehaviorLog(constant.SCREEN_COMBOBOX_DETAIL_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.popover = this.popoverCtrl.create(DetailPopUpComponent, { level: this.changeId });
    this.popover.present({
      ev: event
    });
    this.popover.onDidDismiss(data => {
      if (data) {
        this.changeId = data;
        if (data == "id1") {
          this.labelText = constant.LABEL_LEVEL_ID1
        }
        else if (data == "id2") {
          this.labelText = constant.LABEL_LEVEL_ID2
        }
        else {
          this.labelText = constant.LABEL_LEVEL_ID3
        }
        this.idChange()
      }
    });

  }

  /**
  * get thumbnail image 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */

  async getThumbnailImage() {
    var param = this.datePipe.transform(Date.now(), 'hhmmss');
    this.arrayHitories.forEach(async element => {
      for (var i = 0; i < this.common.listFile.length; i++) {
        var filePath = this.common.listFile[i].fullPath;
        var filename = filePath.split('/').pop();
        var extension = filename.split('.').pop();
        if (extension == constant.PNG_EXTENSION) {
          if (filePath.indexOf(element.ContentCode + '/' + element.ContentCode + '/' + filename) != -1) {
            var fullFilePath = this.file.dataDirectory.slice(0, -1) + filePath;
            fullFilePath = fullFilePath.substr(7, fullFilePath.length);
            fullFilePath = fullFilePath + '?currentTime=' + param;
            element.url = this.sanitizer.bypassSecurityTrustResourceUrl(fullFilePath);
            break;
          }
        }
      }
    });

  }

}


