import { Component } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular/index';

/**
 * Generated class for the FilterByYamatoLanguageTagPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'filter-by-yamato-language-tag-popover',
  templateUrl: 'filter-by-yamato-language-tag-popover.html'

})
export class FilterByYamatoLanguageTagPopoverComponent {

  callbackTags: any;
  itemselect: any

  constructor(private params: NavParams, 
    private viewCtrl: ViewController) {

    console.log('Hello FilterByYamatoLanguageTagPopoverComponent Component');
    this.callbackTags = this.params.get('cba');
    this.itemselect = this.params.get('itemSelect');

  }

  /**
  * go to detail screen 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  goToDetails() {

    let returnArray: any
    this.callbackTags.forEach(element => {
      if (element.Id == this.itemselect) {
        returnArray = element;
      }
    });
    this.viewCtrl.dismiss(returnArray);

  }

  /**
  * action cancel button 
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss();

  }

}



