import { Component } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';
import { NativeStorage } from '@ionic-native/native-storage';
import { ViewController } from 'ionic-angular';
import { UserListPage } from '../user-list/user-list';
import { PlayListPage } from '../play-list/play-list';
import { ChatModePage } from '../chat-mode/chat-mode';
import { SettingPage } from '../setting/setting';
import { ContentsCommentaryPage } from '../contents-commentary/contents-commentary';
import { UserDetailPage } from '../user-detail/user-detail';
import { Events } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { NoAhPlayingPage } from '../no-ah-playing/no-ah-playing';
import { commonClass } from '../../app/common';
import { File } from '@ionic-native/file';
import { constant } from './../../app/constant';

/**
 * Generated class for the TabsPage tabs.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html'
})
export class TabsPage {
  active_background: string = 'url("assets/image/NowakaButton/active_button.png") no-repeat center';
  rootPage: any;
  deactive_background: string = 'url("assets/image/NowakaButton/deactive_button.png") no-repeat center';
  chatpage: string;
  playlistpage: string;
  userlistpage: string;
  disable: string;
  token: string;
  callEvent: Events;

  constructor(public viewCtr: ViewController,
    public navCtrl: NavController,
    public events: Events,
    public nativeStorage: NativeStorage,
    private common: commonClass,
    public file: File,
    public loadingCtrl: LoadingController) {

    this.initial();
    this.callEvent = events;
    events.subscribe('changeLoginPage', () => {
      this.loginPage();
    });
    events.subscribe('chatPage', () => {
      this.chatPage();
    });

  }

  /**
  * initial data
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async initial() {

    let loading = this.loadingCtrl.create({
      spinner: constant.DOTS_TYPE,
      content: constant.REGISTER_LOADING_TEXT
    });
    loading.present();
    // Get token of login user
    this.common.tokenObj = await this.common.getToken();
    // Get all content file.
    this.common.listFile = [];
    var directoryFolder = this.file.dataDirectory + 'contents/';
    await this.common.getAllFile(directoryFolder);
    loading.dismiss();
    // Set chat mode to root page
    this.rootPage = ChatModePage;
    this.chatpage = this.active_background;
    this.playlistpage = this.deactive_background;
    this.userlistpage = this.deactive_background;
    this.disable = "chat";

  }

  ionViewWillEnter() {

    this.events.subscribe('changeContentsCommentaryIntoRootPage', (item, listItem) => {
      this.contentsCommentaryPage(item, listItem);
    });
    this.events.subscribe('changeUserDetailIntoRootPage', (item) => {
      this.userDetailPage(item);
    });
    this.events.subscribe('goToNoAHPlayingPage', (item) => {
      this.noAHPlayingPage(item);
    });

  }

  ionViewWillLeave() {

    this.events.unsubscribe('goToNoAHPlayingPage');
    this.events.unsubscribe('changeUserDetailIntoRootPage');
    this.events.unsubscribe('changeContentsCommentaryIntoRootPage');

  }

  /**
  * set up chat page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  chatPage() {

    if (this.disable != 'chat') {
      this.rootPage = ChatModePage;
      this.chatpage = this.active_background;
      this.playlistpage = this.deactive_background;
      this.userlistpage = this.deactive_background;
      this.disable = "chat";
    }

  }

  /**
  * set up playlist page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  playlistPage() {

    if (this.disable != 'playlist') {
      this.rootPage = PlayListPage;
      this.playlistpage = this.active_background;
      this.userlistpage = this.deactive_background;
      this.chatpage = this.deactive_background;
      this.disable = "playlist";
    }

  }
  /**
  * set up userlist page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  userlistPage() {

    if (this.disable != 'userlist') {
      this.rootPage = UserListPage;
      this.userlistpage = this.active_background;
      this.chatpage = this.deactive_background;
      this.playlistpage = this.deactive_background;
      this.disable = "userlist";
    }

  }

  /**
  * set up setting page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  settingPage() {

    this.navCtrl.push(SettingPage);

  }

  /**
  * set up content commentary page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  contentsCommentaryPage(item, listItem) {

    this.navCtrl.push(ContentsCommentaryPage, {
      idItem: item,
      listItem: listItem
    });

  }

  /**
  * set up login page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  loginPage() {

    this.common.saveValueLocalStorage('token', null);
    this.rootPage = LoginPage;
    this.navCtrl.setRoot(this.rootPage);

  }

  /**
  * set up user detail page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  userDetailPage(item) {

    this.navCtrl.push(UserDetailPage, {
      idItem: item
    });

  }

  /**
  * set up noAh page
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  noAHPlayingPage(item) {

    this.navCtrl.push(NoAhPlayingPage, {
      user: item
    });

  }
  
  /**
  * log out function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  logout() {
    
    this.rootPage = LoginPage;
    this.navCtrl.setRoot(this.rootPage);
  }
}



