import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { Slides } from 'ionic-angular';
import { File } from '@ionic-native/file';
import { commonClass } from './../../app/common';
import { DomSanitizer } from '@angular/platform-browser';
import { constant } from './../../app/constant';
import { Events } from 'ionic-angular';
import { DatePipe } from '@angular/common';

/**
 * Generated class for the WalkThroughPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'walkthrough-page',
  templateUrl: 'walkthrough.html',
})
export class WalkThroughPage {
  @ViewChild(Slides) slides: Slides;

  indexes: any[];
  currentIndex = 0;
  minIndex = 0;
  maxIndex = 0;
  preBtn: HTMLElement;
  nextBtn: HTMLElement;
  callEvent: Events;
  datePipe: any;
  isFirstTimeStart: any;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public file: File,
    public common: commonClass,
    private sanitizer: DomSanitizer,
    public events: Events) {

    this.callEvent = events;
    this.indexes = [];
    this.datePipe = new DatePipe('jp');
    this.isFirstTimeStart = this.navParams.get('isFirstTimeStart');

  }

  /**
   * called when ion view is loaded successfully
   */
  async ionViewDidLoad() {

    await this.common.writeToBehaviorLog(constant.SCREEN_WALKTHROUGH_CODE, constant.MSG_BEHAVIOR_BLOG);
    await this.getAllContensImage();
    // initialize slide buttons
    this.preBtn = document.getElementById(constant.PRE_BUTTON_ID);
    this.nextBtn = document.getElementById(constant.NEXT_BUTTON_ID);
    await this.slides.lockSwipes(true);

  }

  /**
   * slide to previous slide
   */
  async onSlidePrevious() {

    await this.slides.lockSwipes(false);
    if (this.currentIndex != this.minIndex) {
      const previousIndex = this.currentIndex - 1;
      await this.goToSlide(previousIndex);
    }
    await this.slides.lockSwipes(true);

  }

  /**
   * slide to next slide
   */

  async onSlideNext() {

    await this.slides.lockSwipes(false);
    if (this.currentIndex != this.maxIndex) {
      const nextIndex = this.currentIndex + 1;
      await this.goToSlide(nextIndex);
    }
    await this.slides.lockSwipes(true);

  }

  /**
   * slide to the slide by specified index
   * @param index : index of target slide
   */
  async goToSlide(index: number) {

    await this.slides.slideTo(index, 100);
    this.indexes[this.currentIndex].background = constant.TRANSPARENT;
    this.currentIndex = index;
    this.indexes[this.currentIndex].background = constant.WHITE;

  }

  /**
   * dismiss window
   */
  async onDismiss() {

  
    if (this.isFirstTimeStart) {
      await this.viewCtrl.dismiss();
      this.callEvent.publish(constant.CHANGE_TAB_PAGE);
    }
    else {
      this.viewCtrl.dismiss();
    }

  }

  /**
  * get content image
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  async getAllContensImage() {
    var param = this.datePipe.transform(Date.now(), 'hhmmss');
    for (var i = 0; i < this.common.listFile.length; i++) {
      var filePath = this.common.listFile[i].fullPath;
      if (filePath.indexOf('/tuto/images/') != -1) {
        var fullFilePath = this.file.dataDirectory.slice(0, -1) + filePath;
        fullFilePath = fullFilePath.substr(7, fullFilePath.length) + '?' + param;
        let image = {
          link: this.sanitizer.bypassSecurityTrustResourceUrl(fullFilePath),
          background: constant.TRANSPARENT
        };
        this.indexes.push(image);
      }
    }
    this.indexes[0].background = constant.WHITE;
    this.maxIndex = this.indexes.length - 1;
    this.currentIndex = 0;
    return true;

  }

}



