import { Component } from '@angular/core';
import { Http } from '@angular/http';
import { Events } from 'ionic-angular';
import { PopoverController } from 'ionic-angular';
import { AlertController, NavParams, ViewController } from 'ionic-angular';
import { JwtHelper } from 'angular2-jwt';
import { constant } from './../../app/constant';
import { commonClass } from './../../app/common';

/**
 * Generated class for the DeletingUserRegistrationPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'deleting-user-registration-popover',
  templateUrl: 'deleting-user-registration-popover.html'
})

export class DeletingUserRegistrationPopoverComponent {

  objectFacilities: any;
  text: string;
  arrayStaffItem: any;
  callEvent: Events;
  callback: any;
  itemselect: any;
  arrSelect: any;
  contentCode: any;
  arraySearch: any;
  searchWord: any;
  public jwtHelper: JwtHelper = new JwtHelper();

  constructor(public common: commonClass,
    private viewCtrl: ViewController,
    public navParams: NavParams,
    private http: Http,
    public popoverCtrl: PopoverController,
    public events: Events,
    private alertController: AlertController) {

    this.loadContent();
    this.callEvent = events
    this.itemselect = this.navParams.get(constant.POS_TEXT);
    this.arrSelect = this.navParams.get(constant.ARR_SELECT_TEXT);
    this.contentCode = this.navParams.get(constant.CONTENT_CODE_TEXT);;

  }

  /**
    * go to detail
    *    param: 
    *      -id: any
    *    return: 
    *      -N/A
  */
  goToDetails(id) {

    let returnArray: any
    this.arrayStaffItem.forEach(element => {
      if (element.Id == id) {
        returnArray = element
      }
    });
    returnArray.pos = this.itemselect.pos;
    returnArray.action = constant.SELECT_TEXT;
    this.viewCtrl.dismiss(returnArray);

  }

  /**
    * load content
    *    param: 
    *      -N/A
    *    return: 
    *      -N/A
  */
  async loadContent() {

    let userObject = this.common.tokenObj;    
    var data;
    data = await this.http.get(constant.API_GET_USER +
      "?UserId=''" +
      "&AgreementId=" +
      userObject.userObject.AgreementId,
      userObject.options).toPromise()
      .catch(async error => {
        await this.common.showModalDialogConfirm(constant.NWK_CK_005, false);
      });
    if (data) {
      this.arrayStaffItem = data.json().items;
      this.arraySearch = this.arrayStaffItem;
      this.arraySearch.forEach(searchEle => {
        this.arrSelect.forEach(selectEle => {
          if (searchEle.Id == selectEle.id) {
            searchEle.check = 1;
          }
        });
      });
    }

  }

  /**
  * search function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  searchTap() {

    let val = this.searchWord;
    if (val && val.trim() != '') {
      this.arraySearch = this.arrayStaffItem.filter((item) => {
        return (item.UserName.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.arrayStaffItem;
    }

  }

  /**
  * search function
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
 */
  getItems(ev: any) {

    let val = ev.target.value;
    if (val && val.trim() != '') {
      this.arraySearch = this.arrayStaffItem.filter((item) => {
        return (item.UserName.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
    else {
      this.arraySearch = this.arrayStaffItem;
    }

  }

  /**
    * delete user function
    *    param: 
    *      -item: object
    *    return: 
    *      -N/A
  */
  deleteUser(item) {

    let returnArray: any;
    returnArray = item;
    returnArray.action = constant.DELETE_TEXT;
    this.viewCtrl.dismiss(returnArray);

  }

  /**
  * action cancel button
  *    param: 
  *      -item: object
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss();

  }

}


