import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PlayingContentPage } from '../playing-content/playing-content';
import { PopoverController, Popover } from 'ionic-angular';
import { DeletingUserRegistrationPopoverComponent } from './../../components/deleting-user-registration-popover/deleting-user-registration-popover';
import { Events } from 'ionic-angular';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { File } from '@ionic-native/file';
import { commonClass } from './../../app/common';
import { DatePipe } from '@angular/common';
import { constant } from './../../app/constant';
import { Http } from '@angular/http';

/**
 * Generated class for the ContentsCommentaryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
interface Player {
  id: number,
  pos: number,
  name: string,
  image: string
}

@IonicPage()
@Component({
  selector: 'page-contents-commentary',
  templateUrl: 'contents-commentary.html',
})

export class ContentsCommentaryPage {
  public idItem: any;
  tabBarElement: any;
  callEvent: Events;
  imgURL: any;
  popover: Popover;
  namePlayer: any
  idUser: any;
  arrayPlayer: Array<Player>;
  urlContent: SafeResourceUrl;
  listContents: any;
  attrName: any;
  datePipe: any;
  constructor(public events: Events,
    public navCtrl: NavController,
    public navParams: NavParams,
    public popoverCtrl: PopoverController,
    private sanitizer: DomSanitizer,
    public file: File,
    public common: commonClass,
    private http: Http) {
    this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
    this.arrayPlayer = [];
    this.idItem = navParams.get("idItem");
    this.listContents = navParams.get("listItem");
    this.attrName = 'selectedUser';
    this.datePipe = new DatePipe('jp');

  }

  async ionViewDidLoad() {

    this.common.writeToBehaviorLog(constant.SCREEN_CONTENT_COMMENTARY_CODE, constant.MSG_BEHAVIOR_BLOG);
    var data = await this.common.loadValueLocalStorage(this.attrName);
    if (data == null) {
      for (let i = 1; i <= 6; i++) {
        let player: Player = {
          id: null,
          pos: i,
          name: '',
          image: ''
        }
        this.arrayPlayer.push(player);
      }
    } else {
      // Get all user from API
      let userObject = this.common.tokenObj;
      var listUsers;
      listUsers = await this.http.get(constant.API_GET_USER + "?UserId=''" + "&AgreementId=" + userObject.userObject.AgreementId, userObject.options).toPromise()
        .catch(async error => {
          await this.common.showModalDialogConfirm(constant.NWK_CK_005, false);
        });
      if (listUsers) {
        listUsers = listUsers.json().items;
        for (var y = 0; y < data.length; y++) {
          var index = this.common.findWithAttr(listUsers, 'Id', data[y].id);
          if (index == -1) {
            data[y].id = null;
            data[y].name = '';
            data[y].image = '';
          }
        }
        this.arrayPlayer = data;
      }
    }
    var url = await this.common.getContentFile(this.idItem.ContentCode + '/' + this.idItem.ContentCode, 'tuto/tuto.html');
    if (url) {
      // Delete temp folder of contents
      await this.deleteTemp();
      // Create new TempFolder of folder tuto for display
      var newFolderName = this.datePipe.transform(Date.now(), 'hhmmss');;
      var currentFolderPath = this.file.dataDirectory + 'contents/' + this.idItem.ContentCode + '/' + this.idItem.ContentCode + '/';
      await this.file.copyDir(currentFolderPath, 'tuto', this.file.dataDirectory, newFolderName)
        .catch(error => {
          console.log('copyDir error: ' + error);
        });
      // Load Iframe
      url = this.file.dataDirectory + newFolderName + '/tuto.html';
      url = url.substr(7, url.length);
      this.urlContent = this.sanitizer.bypassSecurityTrustResourceUrl(String(url));
    } else {
      this.urlContent = url;
    }

  }

 /**
   * delete folder temp
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  async deleteTemp() {

    var currentFolder;
    currentFolder = await this.file.listDir(this.file.dataDirectory, '').catch(
      error => {
        console.log(error);
      });
    if (currentFolder) {
      for (let subFolder of currentFolder) {
        if (!subFolder.isFile && subFolder.name != 'contents') {
          await this.file.removeRecursively(this.file.dataDirectory, subFolder.name)
            .catch(err => {
              console.log('removeRecursively error: ' + err);
            });
        }
      }
    }

  }

   /**
   * show pop up delete user
   *    param: 
   *      ev: event
   *      item: object
   *    return: 
   *      -N/A
  */
  presentPopover(ev, item) {

    this.common.writeToBehaviorLog(constant.SCREEN_DELETE_USER_CODE, constant.MSG_BEHAVIOR_BLOG);
    this.popover = this.popoverCtrl.create(DeletingUserRegistrationPopoverComponent, { pos: item, arrSelect: this.arrayPlayer, contentCode: 'content' + this.idItem.ContentCode }, { cssClass: 'custom-popover' });
    this.popover.present({
      ev: ev
    });
    this.popover.onDidDismiss(async data => {
      if (data) {
        // Update array select user
        if (data.action == constant.SELECT_TEXT) {
          for (let i = 0; i < this.arrayPlayer.length; i++) {
            if (data.pos == this.arrayPlayer[i].pos) {
              this.arrayPlayer[i].id = data.Id;
              this.arrayPlayer[i].name = data.UserName;
              this.arrayPlayer[i].image = 'url("' + data.PhotoURL + '") no-repeat center';
            }
          }
          // Save information of selected user to local storage.
          await this.common.saveValueLocalStorage(this.attrName, this.arrayPlayer);
        }
        // Remove user
        else {
          if (data.pos == 1) {
            this.arrayPlayer[0].id = null;
            this.arrayPlayer[0].name = '';
            this.arrayPlayer[0].image = '';
          }
          else if (data.pos == 2) {
            this.arrayPlayer[1].id = null;
            this.arrayPlayer[1].name = '';
            this.arrayPlayer[1].image = '';
          }
          else if (data.pos == 3) {
            this.arrayPlayer[2].id = null;
            this.arrayPlayer[2].name = '';
            this.arrayPlayer[2].image = '';
          }
          else if (data.pos == 4) {
            this.arrayPlayer[3].id = null;
            this.arrayPlayer[3].name = '';
            this.arrayPlayer[3].image = '';
          }
          else if (data.pos == 5) {
            this.arrayPlayer[4].id = null;
            this.arrayPlayer[4].name = '';
            this.arrayPlayer[4].image = '';
          }
          else if (data.pos == 6) {
            this.arrayPlayer[5].id = null;
            this.arrayPlayer[5].name = '';
            this.arrayPlayer[5].image = '';
          }
          // Save information of selected user to local storage.
          await this.common.saveValueLocalStorage(this.attrName, this.arrayPlayer);
        }
      }
    });

  }
 /**
   * delete user
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  async deleteUser() {

    for (let i = 0; i < this.arrayPlayer.length; i++) {
      this.arrayPlayer[i].id = null;
      this.arrayPlayer[i].name = '';
      this.arrayPlayer[i].image = '';
    }
    // Save information of selected user to local storage.
    await this.common.saveValueLocalStorage(this.attrName, this.arrayPlayer);

  }
 /**
   * show Playing content
   *    param: 
   *      -N/A
   *    return: 
   *      -N/A
  */
  pushToPlayingContent() {

    this.navCtrl.push(PlayingContentPage, {
      contentId: this.idItem.Id,
      contenCode: this.idItem.ContentCode,
      contenName: this.idItem.ContentName,
      categoryId: this.idItem.CategoryId,
      arrayPlayer: this.arrayPlayer,
      listContents: this.listContents
    })
  }
}



