import { Component } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular/index';

/**
 * Generated class for the FacilityPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'facility-popover',
  templateUrl: 'facility-popover.html'
})
export class FacilityPopoverComponent {

  callback: any;
  text: string;
  arrayItem: any;
  itemselect: any
  idText: any;

  constructor(private params: NavParams, 
    private viewCtrl: ViewController) {

    this.callback = this.params.get('cbbFacilities');
    this.itemselect = this.params.get('itemSelect')
    this.arrayItem = this.callback.items

  }

  /**
  * go to detail screen
  *    param: 
  *      -item: object
  *    return: 
  *      -N/A
  */
  goToDetails() {

    let returnArray: any
    this.arrayItem.forEach(element => {
      if (element.Id == this.itemselect) {
        returnArray = element
      }
    });
    this.viewCtrl.dismiss(returnArray)

  }

  /**
  * action cancel button
  *    param: 
  *      -item: object
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss();

  }

}



