import { Component, } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular/index';

/**
 * Generated class for the FilterBySeriesPopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'filter-by-series-popover',
  templateUrl: 'filter-by-series-popover.html'
})
export class FilterBySeriesPopoverComponent {

  callback: any;
  itemselect: any;

  constructor(
    private params: NavParams, 
    private viewCtrl: ViewController) {

    this.callback = this.params.get('cbb');
    this.itemselect = this.params.get('itemSelect')

  }

  /**
  * go to detail screen
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  goToDetails() {

    let returnArray: any
    this.callback.forEach(element => {
      if (element.Id == this.itemselect) {
        returnArray = element
      }
    });
    this.viewCtrl.dismiss(returnArray);

  }

  /**
  * action cancel button
  *    param: 
  *      -N/A
  *    return: 
  *      -N/A
  */
  cancelTap() {

    this.viewCtrl.dismiss();

  }

}



