import { Injectable } from "@angular/core";
import { Http, XHRBackend, RequestOptions, RequestOptionsArgs, Request, Response } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Events } from "ionic-angular";

@Injectable()
export class AuthenticatedHttpService extends Http {
    public self = this;

    constructor(backend: XHRBackend, defaultOptions: RequestOptions, public events: Events) {
        super(backend, defaultOptions);
    }

    request(url: string | Request, options?: RequestOptionsArgs): Observable<Response> {
        return super.request(url, options).catch(error => {
            if (error.status === 401 || error.status === 403) {
                this.events.publish('changeLoginPage');
            }
            return Observable.throw(error);
        });
    }
}


